.lang-rtl {
  * {
    direction: rtl !important;
  }
  main {
    text-align: right !important;
  }
}

body {
  font-family: $primary-font;
  font-size: 14px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $gray-color;
  line-height: 1.25em;
  letter-spacing: 0.5px;
}
ul {
  list-style: none;
  padding-left: 0;
}
a{
  outline: none;
  &:focus {
    text-decoration: none;
  }
}
a:hover {
  color: $brand-primary;
  text-decoration: none;
}
p {
    color: #332233;
    font-weight: 300;
    font-size: 14px;
    letter-spacing: .8px;
    line-height: 20px;
}
.dropdown-item:focus,
.dropdown-item:hover {
  background: none;
}
.color,
.custom-checkbox input[type="checkbox"] + span.color {
  width: 1.25rem;
  height: 1.25rem;
  border-radius:50%;
  cursor: pointer;
  background: #fafafa none repeat scroll 0 0;
  display: inline-block;
  justify-content: center;
  margin-right: 5px;
  width: 20px;
  border: 1px solid #aaa; 
  &.active,
  &:hover {
    border-color:#333;
  }
}
.facet-label {
  &.active,
  &:hover {
    .custom-checkbox span.color {
      border-color:#333;
    }
  }
}
.btn-primary,
.btn.disabled,
.btn:disabled,
.btn-secondary  {
  opacity: 1;
  text-transform:capitalize;
  font-weight: 400;
  padding: 7px 20px;
  text-transform: uppercase;
  border-radius: 0px;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  color:$light-color;
  background-color: $brand-primary;
  border:1px solid $brand-primary;
  outline: none;
  font-weight: 14px;
  line-height: 21px;
  font-family: $primary-font;
  &:hover, &:active, &:focus{
    color: $light-color;
    background-color:$brand-secondary;
    border-color:$brand-secondary;
    outline: none;
  }
  &:active{
    &:hover{
      color:$light-color;
      background-color:$brand-secondary;
      border-color:$brand-secondary;
    }
    &:focus{
      color:$light-color;
      background-color:$brand-secondary;
      border-color:$brand-secondary;
      outline: none;
    }
  }
  .material-icons {
    margin-right: $small-space;
    width: auto;
    height: auto;
    font-size: 22px;
    line-height: 18px;
    position: relative;
    top: -1px;
  }
}
.btn-tertiary {
  @extend .btn-secondary;
  text-transform: lowercase;
  color: $gray;
  padding: 10px 15px;
  margin: rem(4px) 0;
  font-weight: 400;
  font-size: $font-size-sm;
  .material-icons {
    font-size: $base-font-size;
  }
}
.card {
  clear: both;
  background-color: $light-color;
  border: 1px solid #eaeaea;
  padding: 15px;
  margin-bottom: 10px;
}
.card-title {
  color: #000;
  font-weight: 500;
  font-size: 16px;
}
label ,.label {
  color: $gray-darker;
  text-align: left;
  font-size: 14px;
}
small.label, small.value {
  font-size: 0.8125rem;
}
.form-control-label {
  padding-top: 0.625rem;
}
.form-control {
  background: $light-color;
  border: 1px solid #eeeeee;
  font-size: 14px;
  border-radius: 0px;
  padding:15px;
  color: $black-color;
  font-family: $secondary-font;
  font-weight: 400;
  outline: none !important;
  &:focus {
    color: $black-color;
    border-color: #ebebeb;
  }
}
.input-group {
  &.focus {
  }
  .form-control:focus {
    outline: none;
  }
  .input-group-btn {
    height: 100%;
    > .btn {
    border: 0;
    box-shadow: none;
    color: #ffffff;
    font-size: 0.6875rem;
    font-weight: normal;
    margin-left: 0;
    padding: 12px 16px;
    text-transform: uppercase;
      &[data-action="show-password"]{
        background: #5f5f5f;
        height: 100%;
      }
    }
  }
}
input, textarea {
  &::-webkit-input-placeholder {
    color: $gray;
  }
  &::-moz-placeholder {
    color: #333;
  }
  &:-ms-input-placeholder {
    color: $gray;
  }
  &:-moz-placeholder {
    color: $gray;
  }
  &:active {
    &::-webkit-input-placeholder {
      color: $gray-darker;
    }
    &::-moz-placeholder {
      color: #333;
    }
    &:-ms-input-placeholder {
      color: $gray-darker;
    }
    &:-moz-placeholder {
      color: $gray-darker;
    }
  }
}
.form-control-select {
  height: 2.625rem;
  -moz-appearance: none;
  -webkit-appearance: none;
  font-size: 14px;
  background: $light-color url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAwCAQAAAD9CzEMAAAAPklEQVR4Ae3TwREAEBQD0V/6do4SXPZg7EsBhsQ8IEmSMOsiuEfg3gL3oXC7wK0bd1G4o8X9F4yIkyQfSrIByQBjp7QuND8AAAAASUVORK5CYII=") no-repeat scroll right 0.5rem center / 1.25rem 1.25rem;
  padding: 0 0.5rem;
  border:1px solid #ededed;
}
.form-control-valign {
  padding-top: 0.5rem;
}
.form-control-comment {
  font-size: 0.875rem;
  padding-top: 0.5rem;
  color: $black-color;
}
.form-control-submit {
  &.disabled {
    background: $brand-info;
    color: white;
  }
}
.form-group {
  &.has-error {
    input,
    select {
    }
    .help-block {
      color: $brand-danger;
    }
  }
}
.group-span-filestyle {
  label {
    margin: 0;
  }
  .btn-default {
    background: $brand-primary;
    color: white;
    text-transform: uppercase;
    border-radius: 0;
    font-size: 0.875rem;
    padding: 0.5rem 1rem;
  }
}
.bootstrap-touchspin {
  width: auto;
  float: left;
  display: inline-block;
  @include box-shadow-light;
  input {
    &:focus {
      outline: none;
    }
    &.form-control,
    &.input-group {
      color: $gray-darker;
      background-color: white;
      height: $input-height;
      padding: 0.175rem 0.5rem;
      font-size: 14px;
      width: 3rem;
      border: $input-btn-border-width solid $input-border-color;
      float: left;
    }
  }
  .btn-touchspin {
    @extend .btn-default;
    background-color: white;
    border: $input-btn-border-width solid $input-border-color;
    height: $input-height / 2 + 0.0625rem;
    &:hover {
      background-color: $gray-light;
    }
  }
  .input-group-btn-vertical {
    color: $gray-darker;
    width: auto;
    float: left;
    .bootstrap-touchspin-up {
      border-radius: 0;
    }
    .bootstrap-touchspin-down {
      border-radius: 0;
    }
    .touchspin-up {
      &:after {
        content: "\E5CE";
      }
    }
    .touchspin-down {
      &:after {
        content: "\E5CF";
      }
    }
    i {
      top: 0.0625rem;
      left: 0.1875rem;
      font-size: 0.9375rem;
    }
  }
}
.custom-radio {
  display: inline-block;
  position: relative;
  width: 18px;
  height: 18px;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid #ddd;
  background: white;
  margin-right: 5px;
  input[type="radio"] {
    opacity: 0;
    cursor: pointer;
  }
  input[type="radio"] {
    &:checked + span {
    display: block;
    background-color: $brand-secondary;
    width: 8px;
    height: 8px;
    left: 4px;
    top: 4px;
    border-radius: 50%;
    position: absolute;
    }
    &:focus + span {
      border-color: $gray;
    }
  }
}
.custom-checkbox {
  position: relative;
  input[type="checkbox"] {
    margin-top: 0.25rem;
    opacity: 0;
    cursor: pointer;
    position: absolute;
    + span {
      margin-right: 3px;
      display: inline-block;
      width: 0.9375rem;
      height: 0.9375rem;
      vertical-align: middle;
      cursor: pointer;
      border: 1px solid #ddd;
      background: #fafafa none repeat scroll 0 0;
      .checkbox-checked {
        display: none;
        margin: -0.25rem -0.125rem;
        font-size: 20px;
        color: $gray-darker;
      }
    }
    &:checked + span {
      .checkbox-checked {
        display: block;
      }
    }
    &:focus + span {
      border-color: $gray;
    }
  }
  label {
    text-align: left;
  }
}
.text-muted {
  font-size: 0.875rem;
}
.done {
  color: $brand-success;
  display: inline-block;
  padding: 0 0.8125rem;
  margin-right: 1.563rem;
}
.thumb-mask {
  > .mask {
    position: relative;
    width: 3.438rem;
    height: 3.438rem;
    overflow: hidden;
    border: $gray-light 1px solid;
    margin: $small-space 0;
    img {
      width: 55px;
      height: 55px;
    }
  }
}
.definition-list {
  dl {
    display: flex;
    flex-wrap: wrap;
    dt {
      font-weight: normal;
    }
    dd,
    dt {
      flex: 0 0 45%;
      background: $gray-light;
      padding: 0.625rem;
      margin: 0.125rem;
      &:nth-of-type(even) {
        background: $gray-lighter;
      }
    }
  }
}
.help-block {
  margin-top: $small-space;
}
.btn.disabled,
.btn.disabled:hover {
  background: $brand-primary;
  border-color: $brand-primary;
  color: $light-color;
}
.alert-warning {
  .material-icons {
    color: $warning;
    font-size: 2rem;
    margin-right: $small-space;
    padding-top: $extra-small-space;
  }
  .alert-text {
    font-size: 0.9375rem;
    padding-top: $small-space;
  }
  .alert-link {
    border-radius: 2px;
    border-width: 2px;
    margin-left: $small-space;
    padding: $extra-small-space $medium-space;
    font-weight: 600;
    font-size: 0.8125rem;
    color: $btn-tertiary-color;
  }
  ul li:last-child .alert-link {
    color: white;
  }
  .warning-buttons {
    margin-top: $extra-small-space;
  }
}
.btn-warning {
  -webkit-transition: all .4s ease-in-out;
  -moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.btn-tertiary-outline {
  color: $btn-tertiary-color;
  background-image: none;
  background-color: transparent;
  border-color: $btn-tertiary-color;
  border: 0.15rem solid $btn-tertiary-color;
  -webkit-transition: all .4s ease-in-out;
  -moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  &:hover {
    border-color: $btn-tertiary-hover;
    color: $btn-tertiary-hover;
  }
}
.alert {
  font-size: 0.8125rem;
}
.nav-item {
  .nav-link,
  .nav-separtor {
    color: $gray;
    font-weight: bold;
    &.active {
      color: $gray-darker;
    }
  }
}

.bootstrap-touchspin {
  box-shadow:none;
}

form input[type="text"], form input[type="email"], form input[type="password"] {
  display: inline-block;
  font-size: 14px;
  min-width: 250px;
  width: 100%;
  border-radius: 0px;
  border: 1px solid #eeeeee;
  color: #a4a4a4;
  padding:0 15px;
  background-color: #ffffff;
  font-family: $secondary-font;
  font-size: 14px;
  font-weight: 400;
  height: 40px;
  @media (max-width: 543px) {/* If media is below 544 */
    min-width: auto;
  }
}
form input[type="submit"]{
  height: 40px;
}


/*** Table ***/
.table-bordered {
  border: 1px solid #eaeaea;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 1px;
  color: #808080;
  line-height: 24px;
 @media (max-width: 991px) {/* If media is below 992 */
    overflow: scroll;
    display: block;
  }
  th, td {
      border: 1px solid #eaeaea;
  }
  thead {
    th {
      color: #111111;
      font-size: 15px;
      vertical-align: bottom;
      border-bottom: 1px solid #eaeaea;
      font-weight: 400;
      background-color: #f9f9f9;
    }
    td {
        border: 1px solid #eaeaea;
    }
  }
  &.table-striped {
    tbody {
      tr{
        &:nth-of-type(2n+1) {
          background-color: transparent;
        }
      }
    }
  }
  td {
    a{
      color: #808080;
      &:hover{
        color:#000000;
        text-decoration: none;
      }
    }
  }
}


.top-wrapper{
  margin-top: 30px;
}



