#ishiheaderlinks_block{
	@include media-breakpoint-down(md) {/* If media is below 992 */
		padding: 5px;
		float: right;
	}
	position: relative;
	display: inline-block;
	vertical-align: middle;
	float: left;
	.bullet{		
		padding: 13px 0;
		margin: 0;
		li{
			display: inline-block;	
			vertical-align: middle;
			position: relative;
			a{
				@include media-breakpoint-down(md) {/* If media is below 992 */
					margin: 0 8px;
				}
				color: $brand-primary;
				font-size: 14px;
				line-height: 16px;
				font-weight: 400;
				padding: 0 15px;
				text-transform: capitalize;
				text-decoration:none;
				-webkit-transition: all 0.3s;
		     	-moz-transition: all 0.3s;
		     	-o-transition: all 0.3s;
		     	transition: all 0.3s;
		     	&:hover {
		     		color: $brand-secondary;
		     	}
			}
			.viewmore {
				&:before {
					content: '\f0d7';
					position: absolute;
					top: 1px;
					right: 0;
					font-family: FontAwesome;
				}			
			}	
			.headerlink-dropdown {
			    position: absolute;
				top: 170%;
				right: 0;
				visibility: hidden;
				background: $light-color;
				overflow: hidden;
				text-align: left;
				padding: 5px;
				opacity: 0;
				border: 1px solid #ebebeb;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-o-transition: all 0.5s;
				transition: all 0.5s;
				ul {
					 
					li {
						line-height: 28px;
						a{
							padding: 0 10px;
						}
					}
				}
			}
			&:hover {
				.headerlink-dropdown {
					top: 120%;
					opacity: 1;
					visibility: visible;
				}
			}
		}		
		&.small {
			display: none;
			@include media-breakpoint-down(lg){
				display: block;
				visibility: hidden;
				opacity: 0;
				padding: 10px;
				top: 120%;
				position: absolute;
				right: 0;
				z-index: 2;
				background: #fff;
				border: 1px solid #ebebeb;
				width: 140px;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-o-transition: all 0.5s;
				transition: all 0.5s;
			}
			li {
				display: block;
				line-height: 28px;
				text-align: left;
				a {
					padding: 0 10px;
					margin: 0;
				}
			}
		}
	}
	.header-link {
		@include media-breakpoint-down(md) {/* If media is below 992 */
			padding: 0;
		}
		cursor: pointer;
		position: relative;
		padding: 3px 0;
		.link-icon {
			@include media-breakpoint-down(md) {
				display: none;
			}
			background: url('../img/menu.png') no-repeat center 7px;
	        width:40px;
	        height:40px;
	        display: inline-block;
	        vertical-align: middle;
	        -webkit-transition: all 0.5s;
	        -moz-transition: all 0.5s;
	        -o-transition: all 0.5s;
	        transition: all 0.5s;
		}
		.link-menu {
			padding: 5px;
			position: relative;
			width: 40px;
			height: 40px;
			display: inline-block;
			vertical-align: middle;
			@include media-breakpoint-down(md) {
				.wrapper-menu {
				    width: 18px;
				    height: 14px;
				    display: flex;
				    position: absolute;
				    top: 13px;
				    left: 12px;
				    flex-direction: column;
				    justify-content: space-between;
				    cursor: pointer;
				    transition: transform 330ms ease-out;
				    &.open {
				      transform: rotate(-45deg);
				      .line-menu {
				        &.start {
				          transform: rotate(-90deg) translateX(3px);
				        }
				        &.end {
				          transform: rotate(-90deg) translateX(-3px);
				        }
				      }
				    }
				    .line-menu {
				    	@include media-breakpoint-down(sm) {/* If media is below 768 */
				          background-color: $light-color;
				        }
					    background-color: $brand-secondary;
					    border-radius: 5px;
					    width: 100%;
					    height: 2px;      
					    &:after {
					        content: '';
					        position: absolute;
					        left: 0;
					        width: 0;
					        height: 2px;
					        border-radius: 5px;
					        background: #000;
					        transition: all 0.3s;
					        transition-delay: 0.1s;
					    }
					    &:nth-child(2n+1) {
					        &:after {
					          transition-delay: 0.2s;
					        }
					    }
					    &:nth-child(2n+2) {
					        &:after {
					          transition-delay: 0.3s;
					        }
					    }
					    &:nth-child(2n+3) {
					        &:after {
					          transition-delay: 0.4s;
					          right: 0;
					          left: auto;
					    	}
					    }
					    &.half {
					        width: 50%;
					    }
					    &.start {
					        transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
					        transform-origin: right;
					    }
					    &.end {
					        align-self: flex-end;
					        transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
					        transform-origin: left;
					    }
				    }    
				}
			}
		}
		.small-title {
			@include media-breakpoint-down(md) {/* If media is below 992 */
				display: none;
			}
			margin: 0;
			padding: 5px;
			font-size: 14px;
			font-weight: 400;
			position: relative;
			display: inline-block;
			vertical-align: middle;
		}
	}
	
	&:hover {
		.header-link {
			/*.wrapper-menu {
		      &.open {
		        .line-menu {
		          &:after {
		            display: none;
		          }
		        }
		      }
		      .line-menu {
		        &:nth-child(2n+1) {
		          &:after {
		            width: 50%;
		          }
		        }
		        &:nth-child(2n+2) {
		          &:after {
		            width: 100%;
		          }
		        }
		        &:nth-child(2n+3) {
		          &:after {
		            width: 50%;
		          }
		        }
		      }      
		    }*/
			.small {
				@include media-breakpoint-down(lg){
					top: 100%;
					opacity: 1;
					visibility: visible;
				}
				@include media-breakpoint-down(md){ 
	              top: 112%;
	            }
			}
		}
	}
}


