section.checkout-step {
  .step-title {
    text-transform: uppercase;
    cursor: pointer;
    font-size: 16px;
    margin-bottom: 0;
    font-weight: 600;
    color: #232323;
  }
  .form-control-label {
    text-align: left;
  }
  .content {
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      padding: 0;
    }
    padding: 0 2.313rem;
    margin-top: 10px;
  }
  .form-footer {
    text-align: center;
    margin: 8px 0;
  }
  .payment-options {
    label {
      display: inline-block;
    }
    .custom-radio {
      margin-right: 10px;
    }
    .payment-option {
      margin-bottom: 0.5rem;
    }
  }
  .step-number {
    display: inline-block;
    padding: 0.625rem;
  }
  .continue {
    margin-top: 10px;
  }
}
#checkout-payment-step {
  .center-block {
      white-space: normal;
  }
}
#cart-summary-product-list {
  .media {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    .media-left {
      img {
        max-width: 90px;
      }
    }
    .product-price {
      font-size: 16px;
      color: $brand-secondary;
    }  
  }  
}
.card-block-summary-details{
    padding-bottom: 15px;
}

.cart-summary-totals{
    padding-top: 15px;
}


