.owl-nav{
	text-align: center;
	-webkit-transition: all 0.4s;
	-moz-transition: all 0.4s;
	-o-transition: all 0.4s;
	transition: all 0.4s;
	.owl-prev, .owl-next{
		@include media-breakpoint-up(md) {/* If media is above 767 */
			left: 0;
			top: 37%;
			position: absolute;
		}
		display: inline-block;
		border: 1px solid #eee;
		background-color: #eee;
    	border-radius: 50%;
    	margin:4px;
    	width: 30px;
		height: 30px;
    	outline: none;
    	&:hover{
    		background-color: $brand-secondary;
    		border-color: $brand-secondary;
    		i{
    			&:after{
    				color: $light-color;
    			}
    		}
    	}
		i{
			line-height: 24px;
			&:after{
				color: #fff;
			    content: "\E314";
			    display: inline-block;
			    font-family: "material icons";
			    font-size: 18px;
			    color: $brand-primary;
			}
		}
	}
	.owl-next{
		@include media-breakpoint-up(sm) {/* If media is above 543 */
			right: 0;
			left: auto;
		}
		i{
			&:after{
				content:"\E315";
			}
		}
	}
}


