#ishitestimonials {
  @include media-breakpoint-down(lg) {/* If media is below 1200 */
    padding: 0 15px;
  }
  @include media-breakpoint-down(md){
    margin-bottom: 30px;
  }
  margin-bottom: 70px;
  color:$brand-primary;
  overflow: hidden;
  position: relative;
  .home-title {
    display: none;
  }
  .ishitestimonials-container {
    display: table-cell;
    .testimonial-img {
      display: inline-block;  
      vertical-align: middle;
      position: relative;
      img{
        max-width: 100%;
        text-align: center;
        margin: 0 auto;
      }
    }  
    .testimonial-desc {
      @include media-breakpoint-down(sm) {/* If media is below 768 */
        text-align: center;
        margin: 0 auto;
        padding-top: 20px;
      }
      @include media-breakpoint-down(lg) {/* If media is below 1200 */
        top: 0;
      }
      position: relative;
      top: 75px;      
      .user-name {
        @include media-breakpoint-down(md) {/* If media is below 992 */
          font-size: 40px;
          line-height: 40px;
        }
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          font-size: 25px;
          line-height: 30px;
          display: inline-block;
          vertical-align: middle;
          margin: 0 10px;
        }
        text-transform: uppercase;   
        font-size: 54px;
        line-height: 50px;
        display: block; 
        color: $brand-primary;
        font-weight: 500; 
        margin-bottom: 15px;    
        font-family: $secondary-font;  
      }
      .user-designation{
        @include media-breakpoint-down(md) {/* If media is below 992 */
          font-size: 24px;
          line-height: 34px;
        }
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          font-size: 17px;
          line-height: 20px;
          display: inline-block;
          vertical-align: middle;
          margin: 0 10px;
        }
        font-size: 32px;
        line-height: 40px;
        display: block;
        text-transform: capitalize;
        color: $brand-primary;
        font-weight: 300; 
      }  
      .user-description {
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          margin-bottom: 30px;
        }
        @include media-breakpoint-down(lg) {/* If media is below 1200 */
          width: 100%;
        }
        margin-bottom: 40px;
        font-weight:400;
        font-size: 16px;
        width: 85%;
        line-height: 24px;
        color: #a4a4a4;
        font-family: $secondary-font;
      }
    }
  }  
  .bx-wrapper {
    margin: 0;
    .bx-controls.bx-has-pager {
        @include media-breakpoint-down(lg) {/* If media is below 1200 */
          position: unset;
          display: block;
          padding: 0;
          margin-top: 20px;
          text-align: center;
          box-shadow: none;
          background: transparent;
			border-radius:0;
        }
        position: absolute;
        top: 20%;
        right: 0;
        background: #fff;
        padding: 25px 20px;
        box-shadow: 0 0px 6px 1px rgba(0, 0, 0, 0.1);
        z-index: 1;
        overflow: hidden;
        border-radius:4px;
      .bx-pager {
        @include media-breakpoint-down(lg) {/* If media is below 1200 */
          width: auto;
        }
        position: relative;
        bottom: 0;
        width: 5px;
        padding: 0;
        .bx-pager-item {
          @include media-breakpoint-down(lg) {/* If media is below 1200 */
            display: inline-block;
            vertical-align: middle;
          }
          .bx-pager-link {
            @include media-breakpoint-down(lg) {/* If media is below 1200 */
              width: 30px;
              height: 5px;
              margin: 0 7px 0 0;
            }
            width: 5px;
            height: 30px;
            margin: 0 0 18px;
            background: $brand-secondary;
            display: inline-block;
            vertical-align: middle;
            border-radius: 0;
            &.active {
              background: $brand-primary;
            }
          }
          &:last-child {
            .bx-pager-link {
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}


