#ishiparallaxbanner {
	overflow: hidden;
	margin-bottom: 70px;
	@include media-breakpoint-down(md){
		margin-bottom: 30px;
	}
	.parallax {
		@include media-breakpoint-down(lg) {/* If media is below 1200 */
			padding: 50px 0;
		}
		padding: 80px 0;
		.parallax-list {
			@include media-breakpoint-down(sm) {/* If media is below 768 */
				float: none;
			}
			float: right;
			text-align: center;
			color: $brand-primary;
			.parallax-offer{
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					margin-bottom: 20px;
				}
				font-size: 22px;
				font-weight: 400;
				line-height: 25px;
				margin-bottom: 40px;
				position: relative;
				letter-spacing: 0.4px;
				color: $brand-secondary;
				text-transform: capitalize;
				font-family: $primary-font;
			}
			.parallax-info{
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					border: 0;
					margin-bottom: 20px;
				}
				border: 15px solid #34d9c0;
				border-bottom: 0;
				margin-bottom: 20px;
				.parallax-title{
					font-size: 64px;
					font-weight: 500;
					line-height: 80px;
					padding: 20px;	
					color: $brand-primary;		
					text-transform:capitalize;
					@media (max-width: 767px) {/* If media is below 768 */
						font-size: 50px;
						line-height:55px;
					}
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						font-size: 35px;
						line-height:40px;
						padding: 0;
					}
					span {
						color: $brand-secondary;
					}
				}
				.parallax-subtitle{
					font-size: 24px;
					font-weight: 400;
					line-height: 50px;
					padding: 0 15px;
					text-transform: uppercase;
					position: relative;
					top: 15px;
					letter-spacing: 3px;
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						font-size: 23px;
						margin-bottom: 10px;
					}
					&:before, &:after {
						@include media-breakpoint-down(xs) {/* If media is below 544 */
							display: none;
						}
						content: '';
						position: absolute;
						left: 0;
						top: 20px;
						width: 115px;
						height: 15px;
						background: #34d9c0;
					}
					&:after {
						right: 0;
						left: auto;
					}
				}	
			}	
			#parallaxcountdown {
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					margin-bottom: 20px;
				}
				margin-bottom: 30px;
			    .countdown-days, .countdown-hours, .countdown-minutes, .countdown-seconds {
			    	display: inline-block;
					vertical-align: middle;
			    }
			    .data {
			    	@include media-breakpoint-down(xs) {/* If media is below 544 */
			    		width: 50px;
						height: 50px;
						padding: 0;
						line-height: 48px;
						font-size: 20px;
					}
				    font-size: 43px;
				    padding: 10px;
				    line-height: 55px;
				    width: 100px;
				    text-align: center;
				    margin: 0 auto;
				}
				.text {
					text-transform: uppercase;
					font-size: 16px;
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						display: none;
					}
				}
				.sale-end{
					text-transform: uppercase;
					font-size: 18px;
				}
				.countdown-seconds {
					.data, .text {
						color: $brand-secondary;
					}
				}
			}	
			> a{
				font-size: 14px;
				color: $light-color;
				background-color: $brand-secondary;
				border: 1px solid $brand-secondary;
				font-weight: 400;    
				padding: 12px 20px;
				margin-top: 0;
				position: relative;
				overflow: hidden;
				border-radius: 4px;
				text-transform: uppercase;
				-webkit-transition: 0.5s all ease;
				-moz-transition: 0.5s all ease;
				-o-transition: 0.5s all ease;	
				transition: 0.5s all ease;
				@media (max-width: 543px){
					padding: 10	px 10px;
				}
				&:hover {
					border-color: $brand-primary;  
					background: $brand-primary;
				}
			}
		}		
	}
}


