#ishiheadercontactblock {
	@include media-breakpoint-down(xs) { /* If media is below 544 */ 
		float: none;
        text-align: center;
        margin: 0 auto;
	}
	cursor: pointer;
	float: right;
	padding: 12px 0;
	margin: 0 15px;
	white-space: nowrap;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	.call-img {
	    background: url(../img/sprite-header.png) no-repeat -16px -18px;
	    width: 40px;
	    height: 40px;
	    display: inline-block;
	    vertical-align: middle;
	}
	.call-num {
	    display: inline-block;
	    vertical-align: middle;
	    padding: 5px;
	}
	&:hover {
		.call-img {
			@include media-breakpoint-up(lg) {/* If media is above 1200 */
				background-position: -16px -68px;
			}
		}
	}
}


