#footer {
	.paymentlogo-container{
		@include media-breakpoint-down(md) {/* If media is below 992 */
	      padding:5px 0;
	      text-align: center;
	    }
		text-align: right;
		padding: 6px 0;
		margin: 0;
		display: block;
		span {
			display: none;
		}
		img{
			@include media-breakpoint-down(sm) {/* If media is below 768 */
				width: 41px;
				margin: 2px 1px;
			}
			padding:0;
			background:transparent;
			margin: 0 2px;
			cursor: pointer;
			box-shadow: none;
			-webkit-transition: all 0.25s;
			-moz-transition: all 0.25s;
			-o-transition: all 0.25s;
			transition: all 0.25s;
			&:first-child{
				margin: 0 2px 0 0;
			}
			&:last-child{
				margin: 0 0 0 2px;
			}
			&:hover{
				opacity: 1;
			}
		}
	}
}


