#content-wrapper{
	#smartblogcat{
		margin-bottom: 30px !important;
		.sdsarticleCat{
			@include media-breakpoint-down(md) {/* If media is below 992 */
				text-align: center;
			}
			display: block;
			width: 100%;
			overflow: hidden;
			margin-bottom: 30px;
			text-align: left;
			&:last-child{
				> div{
					margin-bottom:0;
				}
			}
			.articleContent{
				margin-bottom: 0;
				img {
					max-width: 100%;
				}
			}
			.smartblog-desc {
				@include media-breakpoint-down(md) {/* If media is below 992 */
					padding: 15px;
				}
				@include media-breakpoint-down(lg) {/* If media is below 1200 */
					top: 0;
				}
				position: relative;
				top: 18px;
				.author {
					@include media-breakpoint-down(md) {/* If media is below 992 */
						float: none;
					}
					float: left;
				}
				.sdsarticleHeader{
					@include media-breakpoint-down(lg) {/* If media is below 1200 */
						margin: 10px 0;
					}
					margin: 15px 0;
					p{
						margin-bottom: 0;
						a{
							font-size: 20px;
							text-transform: uppercase;
							color: $brand-primary;
							font-weight: 400;
							text-decoration: none;
							@media (max-width: 543px) {/* If media is below 544 */
								font-size: 17px;
							}
						}
					}
				}
				.blogdetail{
					@include media-breakpoint-down(lg) {/* If media is below 1200 */
						margin-bottom: 10px;
					}
					margin-bottom: 15px;
					display: block;
					span{
						font-size: 15px;
						display: inline-block;
						vertical-align: middle;
						margin-right: 10px;
						position: relative;
						text-transform: capitalize;
						color: $brand-secondary;
						line-height: 25px;
						@media (max-width: 543px) {/* If media is below 544 */
							font-size: 14px;
						}
						a,i{
							color: $brand-secondary;
						}
					}
				}
				.sdsarticle-des{
					@include media-breakpoint-down(lg) {/* If media is below 1200 */
						margin-bottom: 10px;
					}
					display: block;
					margin-bottom: 15px;
					color: #666;
					line-height: 24px;
				}
				.sdsreadMore{
					a{
						text-decoration: none;
						font-size: 14px;
						font-weight: 400;
						background: $brand-secondary;
						color: #fff;
						text-transform: uppercase;
						padding: 5px 15px;
						border-radius: 0;
						display: inline-block;
						vertical-align: middle;
						-webkit-transition: all 0.5s;
						-moz-transition: all 0.5s;
						-o-transition: all 0.5s;
						transition: all 0.5s;
						&:hover {
							background: $brand-primary;
						}
					}
				}
			}
		}
	}
	.blog_pagination{
		.pagination{
			margin-top:0;
			margin-bottom: 20px;
			padding: 15px;
			.results{
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					float: none;
					text-align: center;
				}
				float: left;
				line-height: 34px;
			}
			.pagination_bottom{
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					float: none;
					text-align: center;
				}
				margin-bottom: 0;
				display: block;
				float: right;
				li{
					display: inline-block;
					vertical-align: middle;
					a{
						padding: 5px;
						text-align: center;
					}
					&.current, &:hover{
						color:$light-color;
						background:$brand-primary;
					}
				}
			}
		}
	}
}


#right-column, #left-column {
section {
    margin-bottom: 15px;
    padding: 0;
    border: 1px solid #eee;
}
#blogarticle-container {
	margin-top: 20px;
}
.description {
	position: relative;
	margin-bottom: 20px;
	padding: 0 10px 20px;
	border-bottom: 1px dotted rgba(0,0,0,0.1);
	&:last-child {
		margin-bottom: 0;
		border-bottom: 0;
	}
	.blog-artical {
		float: left;
		display: inline-block;
		margin-right: 8px;
		img {
			max-width:80px;
		}
	}
	.blog-desc {
		display: inline-block;
		width: 60%;
		@media screen and (min-width: 992px) and (max-width: 1199px) {/* If media is between 992 and 1199 */
			width: 50%;
		}
		p {
			color: #151515;
			font-size: 15px;
			margin-bottom: 10px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.smart-desc {
			font-size: 12px;
			color: #666666;
			font-style: italic;
			.meta-date {
				font-size: 12px;
				color: #666666;
				font-style: italic;
				display: inline-block;
				vertical-align: middle;
			}
			.meta-likes {
				display: inline-block;
				vertical-align: middle;
				font-size: 12px;
				color: #666666;
				font-style: italic;
				i {
					span {
						color: #666666;
					}
					color: #F27482;
					font-size: 12px;
					&:hover {
						color: #ED4B5C;
					}
				}
			}
		}				
	}
}
.popular-blog {
	#tag-container {
		margin: 18px 10px;
	}
	ul {
		li {
			a{
				color: $brand-primary;
			}
			display: inline-block;
			vertical-align: middle;
			padding: 6px 15px;
			border: 1px solid #eee;
			margin: 4px 2px;
			cursor: poiner;
			-webkit-transition: all 0.25s;
	    	-moz-transition: all 0.25s;
	    	-o-transition: all 0.25s;
	    	transition: all 0.25s;
			&:hover {
				a {
					color: $light-color;
				}
				background-color: $brand-secondary;
				border-color: $brand-secondary;
			}
		}
	}
}
}	



