/*========================== NO CHANGE EXCEPT PADDING AND MARGIN==========================*/

#_desktop_top_menu {
  @include media-breakpoint-down(md) {/* If media is below 992 */
    display: block;
  }
  position: relative;
  margin: 0;
  float: left;
  padding: 0 15px;
  display: inline-block;
  vertical-align: middle;
  .home-title {
    background-color: $brand-secondary;
    color: $light-color;
    font-size: 14px;
    font-weight: 500;      
    text-align: left;
    padding: 15px 15px 15px 45px;
    margin: 0;
    text-transform: uppercase;
    width: 269px;
    line-height: 16px;
    letter-spacing: 1.5px;
    cursor: pointer;
    border: 0;
    background-image: none;
    position: relative;
    &:after {
      display: none;
    }
  }
  .wrapper-menu {
    width: 18px;
    height: 14px;
    display: flex;
    position: absolute;
    top: 16px;
    left: 30px;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: transform 330ms ease-out;
    &.open {
      transform: rotate(-45deg);
      .line-menu {
        &.start {
          transform: rotate(-90deg) translateX(3px);
        }
        &.end {
          transform: rotate(-90deg) translateX(-3px);
        }
      }
    }
    .line-menu {
      background-color: #fff;
      border-radius: 5px;
      width: 100%;
      height: 2px;      
      &:after {
        content: '';
        position: absolute;
        left: 0;
        width: 0;
        height: 2px;
        border-radius: 5px;
        background: #000;
        transition: all 0.3s;
        transition-delay: 0.1s;
      }
      &:nth-child(2n+1) {
        &:after {
          transition-delay: 0.2s;
        }
      }
      &:nth-child(2n+2) {
        &:after {
          transition-delay: 0.3s;
        }
      }
      &:nth-child(2n+3) {
        &:after {
          transition-delay: 0.4s;
          right: 0;
          left: auto;
        }
      }
      &.half {
        width: 50%;
      }
      &.start {
        transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
        transform-origin: right;
      }
      &.end {
        align-self: flex-end;
        transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
        transform-origin: left;
      }
    }    
  }
  &:hover {
    .wrapper-menu {
      &.open {
        .line-menu {
          &:after {
            display: none;
          }
        }
      }
      .line-menu {
        &:nth-child(2n+1) {
          &:after {
            width: 50%;
          }
        }
        &:nth-child(2n+2) {
          &:after {
            width: 100%;
          }
        }
        &:nth-child(2n+3) {
          &:after {
            width: 50%;
          }
        }
      }      
    }
  }
}

#top-menu {
  @include media-breakpoint-down(md) {
    background-color: none;
  }
  position: absolute;
  top: 46px;
  left: 15px;
  right: 15px;
  z-index: 9;
  display: none;
  background-color: #f5f5f5;
  > li {
    display: block;
    text-align: left;
    position: relative;
    border-bottom: 1px solid #ddd;
    &:last-child {
      border-bottom: 0;
    }   
    @include media-breakpoint-down(md) {
      padding: 2px 0;
      border-bottom: 0;
    }
    .dropdown-item {
        white-space: normal;
    }
    > a {
      @include media-breakpoint-down(md) {
        color: $brand-primary;
      }
      padding: 15px;
      font-weight: 400;
      color: $brand-primary;
      text-transform: capitalize;
      i {
        float: right;
        line-height: 16px;
      }
    }
    #_desktop_top_menu & {
      .sub-menu {
        &.collapse {
          opacity: 0;
          -webkit-transition: opacity .4s,left .4s,right .4s,visibility .4s;
          -o-transition: opacity .4s,left .4s,right .4s,visibility .4s;
          transition: opacity .4s,left .4s,right .4s,visibility .4s;
          box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
        }
        top: 5px;
        left: 115%;
        border: none;
        width: auto;
        z-index: 18;
        padding: 15px 0;
        border-radius: 0;
        visibility: hidden;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        text-align: center;
        ul[data-depth="1"] > li {
          text-align: left;
          float: left;
          margin-left: 20px;
          min-width: 182px;
          > a{
            padding: 0;
            padding-bottom: 4px;
            margin-bottom: 17px;
            color: $brand-secondary;
            text-transform: capitalize;
            font-size: 15px;
            position: relative;
            &:before{
              border-top: 1px solid #ddd;
              bottom: -7px;
              content: "";
              position: absolute;
              left: 0;
              width: 50%;
            }
          }
        }
        a:hover {
          color: $brand-secondary;
        }
      }
    }
    &:hover {
      #_desktop_top_menu & { 
        &:hover {
          .sub-menu {
            left: 100%;
            opacity: 1;
            visibility: visible; 
          }
        }
      }
    }
  } 
}
.top-menu {
  a.dropdown-submenu {
    color: $brand-primary;
    text-transform: uppercase;
    font-weight: 400;
  }
  ul[data-depth="2"]{
    > li{
      a{
        padding: 0;
        padding-bottom: 12px;
        font-size: 13px;
        color: #333;
        font-weight: 400;
      }
      &:last-child{
        a{
          padding-bottom: 0;
        }  
      }
    }
  }  
  .collapse {
    display: inherit;
  }
  .category-image {
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
    margin-top: 25px;
  }
  .popover {
    max-width: inherit;
  }
}
.popover.bs-tether-element-attached-top {
  margin-top: 0;
}

#canvas {
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

#mobile_top_menu_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  bottom: 0;
  background: white;
  z-index: 999;
  overflow-y:auto;
  overflow-x: auto;
  #top_menu_closer {
    cursor: pointer;
    color:$black-color;
    margin-top: 8px;
    margin-left: 8px;
    float: left;
  }
  #_mobile_top_menu {
    padding:10px;
  }
  #top-menu {
    margin-bottom: 0;
    text-align:left;
    display: block;
    padding: 2px 0;
    background: none;
    li{
      a{
        display: inline-block;
        width: auto;
      }
    }
  }
  .top-menu {
    color: $black-color;
    .category-image {
      display: none;
    }
    .collapse-icons[aria-expanded="true"] {
      .add {
        display: none;
      }
      .remove {
        display: block;
      }
    }
    .collapse-icons .remove {
      display: none;
    }
    .navbar-toggler {
      padding: 0;
      position: absolute;
      cursor: pointer;
      right: 0;
    }
    a[data-depth="0"] {
      padding: 0;
      margin-bottom: 8px;
    }
    .collapse {
      display: none;
      &.in {
        display: block;
      }
    }
    .sub-menu {
      &.collapse {
        display: none;
        &.in {
          display: block;
        }
      }
      box-shadow: none;
      z-index: inherit;
      display: block;
      position: static;
      overflow: hidden;
      margin-left: 0;
      width: 100%;
      min-width: 100%;
      border:none;
      ul[data-depth="1"] {
        margin: 0;
        padding:0;
        > li {
          float: none;
          margin: 0;
          a {
            text-transform: capitalize;
          }
        }
      }
      ul {
        padding: 0 0 0 15px;
      }
      li > a {
        margin-bottom: 5px;
      }
      ul[data-depth="2"] li a {
        padding-left: rem(20px);
        padding-bottom: 5px;
      }
      ul[data-depth="3"] li a {
        padding-left: rem(40px);
      }
      ul[data-depth="4"] li a {
        padding-left: rem(60px);
      }
    }
    .popover {
      border-radius: 0;
    }
  }
}
#menu-icon {
  @include media-breakpoint-down(sm) {/* If media is below 768 */
    color: $light-color;
  }
  color: $brand-secondary;
  padding: 13px 15px;
  font-size: 24px;
}



