/*** WRAPPER ***/
#wrapper {
	padding: 30px 0;
	#index & {
	  padding: 0;
	}
	.banner {
	  margin-bottom: 1.5rem;
	  display: block;
	}
}

/*** BREADCRUMB ***/
.breadcrumb-container {
    @include media-breakpoint-down(sm) {
      padding: 0;
    }
    padding-top: 60px;
    background: url(../img/breadcrumb.jpg) no-repeat top;
    background-attachment: fixed;
    .breadcrumb {
	    background: transparent;
	    padding: 30px 0 40px;
	    margin-bottom: 0;
	    text-align: center;
	    .breadcrumb-title {
		    font-size: 22px;
		    line-height: normal;
		    margin-bottom: 14px;
		    color: #000;
		    text-transform: capitalize;
		}
		ul {
			margin: 0;
		}
		li {
			display: inline-block;
			vertical-align: middle;
			&:after {
				content: "-";
				color: #000;
				margin: .3125rem;
			}
			&:last-child {
				&:after {
					content: '';
				}
			}
		}
	}
}

/******* Pagination *******/
.pagination {
	width: 100%;
	margin-top: 15px;
	margin-bottom:0;
	padding: 14px 0;
	background-color: #f5f5f5;
	.pagination-desc{
		line-height: 50px;
		font-size:14px;
		color:$black-color;
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			text-align:center;
		}
	}
	.pagination-right{
		float: right;
		text-align: right;
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			text-align:center;
		}
	}
	.page-list {
		padding: rem(10px) 0;
		margin-bottom: 0;
		li {
			display: inline-block;
			vertical-align: middle;
			text-align: center;
		}
		.pagination-lbl {
			display: none;
		}
	}
	a {
		margin: 0 auto;
		font-weight: 600;
		border:1px solid #eee;
		width: 35px;
		height: 35px;
		padding: 5px;
		line-height: 22px;
		text-align: center;
		display: block;
		color: $brand-primary;
		background-color: $light-color;
		box-shadow: 0 0 5px -1px rgba(0,0,0,.1);
		&:hover {
			color:$light-color;
			background-color: $brand-primary;
		}
		&:not(.previous):not(.next) {
			letter-spacing: 0.125rem;
		}
	}
	.previous {
		float: left;
		padding: 5px;
	}
	.next {
		float: right;
		padding: 5px;
	}
	.disabled {
		cursor: not-allowed;
	}
	.current a {
		background-color: $brand-secondary;
		text-decoration: none;
		color: $light-color;
	}
}

/****** Category Page ********/


.block-category {
	margin-bottom: 1.563rem;
	padding: 0;
	border: none;
	.category-cover {
		img {
			max-width: 100%;
		}
	}
}
	.category-info {
		.h1 {
		    text-transform: capitalize;
		    font-size: 32px;
		    font-weight: 600;
		    color: #232323;
		    @media (max-width: 543px) {/* If media is below 544 */
		    	font-size: 26px;
		    	line-height: 1;
		    }
		}
		#category-description {
			margin-bottom: 1rem;
		    p {
		      	color: #323;
				font-weight: 300;
				font-size: 14px;
				letter-spacing: .8px;
				line-height: 20px;
			    margin-bottom: 0;
			    &:first-child {
		        	margin-bottom: $medium-space;
		      	}
		    }
		}
	}
	#products{
		&.category-product-info{ 
		    padding: 0;
		    color: #7a7a7a;
		}
	}	
	#product-list-top {
	  padding: 14px 15px;
	  background-color: #f5f5f5;
	  margin-bottom: 15px;
	  .products-selection {
	    .title {
	      color: $gray-dark;
	    }
	  }
	}
	.products-selection {
		.sort-by-row {
			display: flex;
			align-items: center;
		}
		.sort-by {
			white-space: normal;
			word-break: break-word;
			margin-right: -0.9375rem;
			margin-left: 0.9375rem;
			text-align: right
		}
		.total-products {
			padding-top: 0.625rem;
			position: relative;
			top: 3px;
			p {
				margin-top: 2px;
			}
		}
		h1 {
			padding-top: 0.625rem;
		}
	}
	.products-sort-order {
		color: $gray;
		.select-title {
			display: inline-block;
			width: 100%;
			color: $gray-darker;
			background: white;
			padding: $small-space;
			cursor: pointer;
			line-height: 22px;
			border: $input-btn-border-width solid $input-border-color;
		}
		.select-list {
			display: block;
			color: $gray-darker;
			padding: $small-space $medium-space;
			&:hover {
				background: $brand-primary;
				color: white;
				text-decoration: none;
			}
		}
		.dropdown-menu {
			left: auto;
			width: 90%;
			background: $gray-lighter;
			border: none;
			border-radius: 0;
			box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.1);
			margin: 0;
		}
	}
	#js-product-list-top {
	  	.layout-options {
		    display: block;
		    float: left;
		    margin-right: 10px;
		    display: inline-block;
		    vertical-align: middle;
		    > span {
		    	width: 22px;
		    	height: 22px;
		    	display: inline-block;
		    	cursor: pointer;
		    	margin: 1px;
		    	-webkit-transition: all 0.25s;
		    	-moz-transition: all 0.25s;
		    	-o-transition: all 0.25s;
		    	transition: all 0.25s;
		    }
		    #grid-view {
		    	background:url('../img/layout_sprite.png') no-repeat 0px 0px;
		    	&:hover {
		    		background-position:0px -33px;
		    	}
		    	&.checked {
		    		background-position:0px -33px
		    	}
		    }
		    #list-view {
		    	background:url('../img/layout_sprite.png') no-repeat -37px 1px;
		    	&:hover {
		    		background-position:-37px -32px;
		    	}
		    	&.checked {
		    		background-position:-37px -32px;
		    	}
		    }
	  	}
	}
	#products {
		.products {
			.product-miniature {
				.product-container {
					.thumbnail-container {
						.thumbnail-inner {
							.product-thumbnail {
								.outstock-overlay {
								    position: absolute;
								    background: rgba(226, 0, 72, 0.3);
								    width: 100%;
								    top: 46%;
								    text-align: center;
								    padding: 10px;
								    color: #000;
								    display: block;
								    font-size: 16px;
								    font-weight: 500;
								    text-transform: uppercase;
								}
			    			}
			    		}
			    	}
			    }
			}
			&.list {
				.product-miniature {
					.product-container {
						.thumbnail-buttons {
							position: unset;
							opacity: 1;
							overflow: hidden;
							-webkit-transition: all .0s;
							-moz-transition: all .0s;
							-o-transition: all .0s;
							transition: all .0s;
							.quickview-btn, .wishlist-btn, .cart-btn {
							    -moz-transform: translateX(0);
							    -webkit-transform: translateX(0);
							    -o-transform: translateX(0);
							    -ms-transform: translateX(0);
							    transform: translateX(0);
							    opacity: 1;
							}
							.cart-btn {
								margin-bottom: 0;
							}
						}
					}
				}
			}
		}
	}
	.open > .dropdown-menu {
	    display: block;
	}


/*** Responsive part ***/
@include media-breakpoint-down(md) {
	#left-column, #right-column {
		width: 100%;
		#search_filter_controls {
			text-align: center;
			button {
				margin: 0 0.5rem;
			}
			margin-bottom: 1rem;
		}
		#search_filters {
			margin-bottom: 15px;
			box-shadow: none;
			padding: 0;
			border-top: 1px solid $gray-lighter;
			.facet {
				.title {
					cursor: pointer;
					color: #232323;
				}
				.block-title,.title {
				    .navbar-toggler {
				      i {
				        &.add {
				          display: none;
				        }
				        &.remove {
				          display: block;
				        }
				      }
				    }
				    &.collapsed{
				      .navbar-toggler {
				        i {
				          &.add {
				            display: block;
				          }
				          &.remove {
				            display: none;
				          }
				        }
				      }
				    }
				}
				padding-top: 0;
				border-bottom: 1px solid $gray-lighter;
				.facet-title {
					color: $gray-darker;
					text-transform: uppercase;
				}
				.h6 {
					margin-bottom: 0;
					padding: 0.625rem;
					display: inline-block;
				}
				.navbar-toggler {
					display: inline-block;
				}
				.collapse {
					display: none;
					&.in {
						display: block;
					}
				}
				.facet-label {
					a {
						margin-top: 0;
					}
				}
				ul {
					margin-bottom: 0;
					li {
						padding: 6px 15px;
					}
					&#facet_91982 {
						margin: 0 15px 15px;
					}
				}				
			}
		}
	}
	#content-wrapper {
		width: 100%;
	}
	#search_filter_toggler {
		width: 100%;
	}
	.products-sort-order {
		.select-title {
			margin-left: 0;
		}
	}
	.products-selection {
		h1 {
			padding-top: 0;
			text-align: center;
			margin-bottom: 1rem;
		}
		.showing {
			padding-top: 1rem;
		}
	}
	#prices-drop #content-wrapper,
	#new-products #content-wrapper,
	#best-sales #content-wrapper {
		width: 100%;
	}
}

@include media-breakpoint-down(xs) {
	.products-selection {
		.filter-button {
			padding-left: 0;
		}
	}
}


