.item{
  &.sold-out{
    .thumbnail-container{
      .thumbnail-inner{
        .product-thumbnail{
          .outstock-overlay{
            position: absolute;
            background: rgba(226,0,72,0.3);
            width: 100%;
            top: 46%;
            text-align: center;
            padding: 10px;
            color: #000;
            display: block;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
          }
        }
      }
    }
  }
}
#products,.featured-products,.product-accessories,.product-miniature  {
  padding:15px;
  position: relative;
  overflow: hidden;
  .ishiproducts-grid & {
    padding: 15px 0;
  }
  .product-thumbnail {
    display: block;
    text-align:center;
  }
  .product-container{  
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      max-width: 370px;
      margin: auto;
    }
    .thumbnail-container {
      position: relative;
      overflow: hidden;
      .thumbnail-inner {
        position: relative;
        .product-flags {          
          margin-bottom: 0;
          position: unset;
          > li {
            position: absolute;
            display: block;
            padding: 5px 7px;
            color: $gray-darker;
            font-family: $secondary-font;
            text-transform: capitalize;
            font-size: 14px;
            font-weight: 400;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
          }
          .discount, .on-sale{
            top: 5px;
            right: 5px;
            @include media-breakpoint-up(sm){
              opacity: 0;
              transform:translate(100px);
              -moz-transform:translate(100px);  
              -webkit-transform:translate(100px);  
              -o-transform:translate(100px);  
              -ms-transform:translate(100px);
            }
          }
          .new {
            top: 5px;
            left: 5px;
            @include media-breakpoint-up(sm){
              -moz-transform:translate(-100px);  
              -webkit-transform:translate(-100px);  
              -o-transform:translate(-100px);  
              -ms-transform:translate(-100px); 
              transform:translate(-100px);
            }
          }
          .online-only {
            left: 5px;
            top: 12px;
            display: none;
            &::before{
                content:"\E30A";
                font-family: 'Material Icons';
                vertical-align: -2px;
                margin: 5px;
                font-size: 12px;
            }
          }
          .pack {
            color: $brand-primary;
            right: 5px;
            top: 30px;
            @include media-breakpoint-up(sm){
              transform:translateX(100px);
              -moz-transform:translateX(100px);  
              -webkit-transform:translateX(100px);  
              -o-transform:translateX(100px);  
              -ms-transform:translateX(100px);
            }
          }
        }
        .product-thumbnail {
          display: block;
          text-align: center;
          position: relative;
          img {
            margin: 0 auto;
            max-width:100%;
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;
          }
          .product-img-extra, &.change {
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            opacity: 0;
            -moz-transform: scale(0,1);
            -webkit-transform: scale(0,1);
            -o-transform: scale(0,1);
            -ms-transform: scale(0,1);
            transform: scale(0,1);
          }
          img {
            margin: 0 auto;
            max-width:100%;
          }   
        }
      }
    }
    .thumbnail-buttons {
      position: relative;
      @media (min-width: 544px) {/* If media is above 543 */
        opacity: 0;
        position: absolute;
        bottom: 15px;
        left: 0;
        right: 0;
        -webkit-transition: all .5s;
        -moz-transition: all .5s;
        -o-transition: all .5s;
        transition: all .5s;
      }
      .quickview-btn, .wishlist-btn ,.cart-btn {
        width: 35px;
        display: inline-block;
        vertical-align: middle;
        height: 35px;
        overflow: hidden;
        @media (min-width: 544px) {/* If media is above 543 */
          opacity: 0; 
        }
        a {
          position: relative;
          padding: 0;
          background-color: $brand-secondary;
          border: 1px solid $brand-secondary;
          border-radius: 50%;
          width: 35px;
          height:35px;
          display: inline-block;
          vertical-align: middle;
          -webkit-transition: all .5s;
          -moz-transition: all .5s;
          -o-transition: all .5s;
          transition: all .5s;
          &:after{
            background: url('../img/product-sprite.png') no-repeat;
            content: "";
            position: absolute;
            height: 35px;
            width: 35px;
            top: 0;
            left: 0;
            display: block;
            margin: 0 auto; 
            -webkit-transition: all .5s;
            -moz-transition: all .5s;
            -o-transition: all .5s;
            transition: all .5s;               
          }
          i {
            color: $brand-secondary;
            display: none;
          }
          span {
            display: none;
          }
        }
        &:hover {
          a{
            background-color: $brand-primary;
            border-color: $brand-primary;
          }
        }
      }
      .quickview-btn{
        -webkit-transition: all .9s ease-in-out;
        -moz-transition: all .9s ease-in-out;
        -o-transition: all .9s ease-in-out;
        transition: all .9s ease-in-out;
        @media (min-width: 544px) {/* If media is above 543 */
          -moz-transform:translateX(-70px);  
          -webkit-transform:translateX(-70px);  
          -o-transform:translateX(-70px);  
          -ms-transform:translateX(-70px); 
          transform:translateX(-70px);
        }
        a{
          &:after{
            background-position:-79px -130px;
          }
        }
      }
      .cart-btn {
        -webkit-transition: all .3s ease-in-out;
        -moz-transition: all .3s ease-in-out;
        -o-transition: all .3s ease-in-out;
        transition: all .3s ease-in-out;
        @media (min-width: 544px) {/* If media is above 543 */
          -moz-transform:translateX(-70px);  
          -webkit-transform:translateX(-70px);  
          -o-transform:translateX(-70px);  
          -ms-transform:translateX(-70px); 
          transform:translateX(-70px);
        }
        a{ 
          &:after{
            background-position:-80px -42px;               
          } 
        } 
      }
      .wishlist-btn {
        -webkit-transition: all .6s ease-in-out;
        -moz-transition: all .6s ease-in-out;
        -o-transition: all .6s ease-in-out;
        transition: all .6s ease-in-out;
        @media (min-width: 544px) {/* If media is above 543 */
          -moz-transform:translateX(-70px);  
          -webkit-transform:translateX(-70px);  
          -o-transform:translateX(-70px);  
          -ms-transform:translateX(-70px); 
          transform:translateX(-70px);
        }
        a {
          &:after{
            background-position:-79px 3px;
          }
        }     
      }
    }
    .product-comments{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0 auto 5px;
      text-align: center;
      .star_content{
        display: block;
        padding: 0;
        &:after{
          display: none;
        }
      }
      .comment_advice{
        display: none;
      }
    }
    .product-desc {
        display: none;
        margin-bottom: 10px;
    }
    &:hover {
      .thumbnail-container {
        .thumbnail-inner {
          .product-thumbnail{
            .change{
              opacity: 1;
              -moz-transform: scale(1,1);
              -webkit-transform: scale(1,1);
              -o-transform: scale(1,1);
              -ms-transform: scale(1,1);
              transform: scale(1,1);
            }
            .product-img-extra{
              #category &{
                opacity: 1;
                -moz-transform: scale(1,1);
                -webkit-transform: scale(1,1);
                -o-transform: scale(1,1);
                -ms-transform: scale(1,1);
                transform: scale(1,1);
              }
            }              
          }
          .product-comments {
              .star_content {
                .star_on {   
                  &:before {
                    top: 0;
                  } 
                }
              }
            }
          .product-flags{
          	.new, .discount, .pack{
              opacity: 1;
              transform:translateX(0px);
              -moz-transform:translateX(0px);  
              -webkit-transform:translateX(0px);  
              -o-transform:translateX(0px);  
              -ms-transform:translateX(0px);
            }
          }          
        }
      }
      .product-description {
        .product-price-and-shipping {
          @include media-breakpoint-down(xs) {/* If media is below 544 */
            opacity: 1;
          }
          opacity: 0;
        }
        .thumbnail-buttons{
          opacity: 1;
          .quickview-btn, .wishlist-btn ,.cart-btn  {
            opacity: 1;
            -moz-transform:translateX(0);  
            -webkit-transform:translateX(0);  
            -o-transform:translateX(0);  
            -ms-transform:translateX(0); 
            transform:translateX(0);
          }
        }
      }
    }
  }
  .products-section-title {
    text-align: center;
    margin-bottom: rem(24px);
  }

  .product-description {
    padding: 10px;
    text-align: center;
    .product-title {
      line-height: 18px;
      margin-bottom: 10px;
      a {
        color: #202020;
        font-size: 14px;
        text-decoration: none;
        text-align: center;
        font-weight: 400;
        text-transform: capitalize;
      }
    }
    .product-price-and-shipping {
      @include media-breakpoint-up(sm){
        opacity: 1;
        -webkit-transition: all .4s;
        -moz-transition: all .4s;
        -o-transition: all .4s;
        transition: all .4s;
      }
      font-family: $primary-font;
      color: $brand-primary;
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 8px;
      span {
        margin-right: 5px;
      }
      .regular-price {
        text-decoration: line-through;
        color: #666666;
      }
      .discount-percentage, .discount-amount {
        color: $brand-secondary;
        margin: 0 5px;
      }
    }
  }
  .variant-links {
    text-align: left;
  }
  img {
    margin-left: 4px;
  }
  .comments_note {
    text-align: center;
    color: $gray;
  }
  .count {
    color: $gray-dark;
    font-weight: 400;
    position: relative;
    bottom: rem(8px);
  }
  .all-product-link {
    clear: both;
    color: $gray-dark;
    font-weight: 400;
    margin-top: rem(24px);
    margin-bottom: rem(24px);
  }
}

#products {
    /*========================== NO CHANGE ==========================*/
  article {
    &:nth-child(3n+1) {
      clear: left;
    }
    @include media-breakpoint-down(lg) {/* If media is below 1200 */
        &:nth-child(3n+1) {
          clear: none;
        }
        &:nth-child(2n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(md) {/* If media is below 992 */
        &:nth-child(2n+1) {
          clear: none;
        }
        &:nth-child(3n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
        &:nth-child(3n+1) {
          clear: none;
        }
        &:nth-child(2n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */
        &:nth-child(2n+1) {
          clear: none;
        }
    }
  }
}

.ishiproducts-grid {
  div {
    &:nth-child(4n+1) {
      clear: left;
    }
    @include media-breakpoint-down(lg) {/* If media is below 1200 */
        &:nth-child(4n+1) {
          clear: none;
        }
        &:nth-child(3n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
        &:nth-child(3n+1) {
          clear: none;
        }
        &:nth-child(2n+1) {
          clear: left;
        }
    }

    @include media-breakpoint-down(xs) {/* If media is below 544 */
        &:nth-child(2n+1) {
          clear: none;
        }
    }
  }
}

/* ---------------------------- scss for list view of product -----------------------------*/
#products .products {
  &.list {
    .product-miniature {
      padding:15px 0;
      .product-container{
        .thumbnail-container {
          .thumbnail-inner {
            overflow:hidden;
            .thumbnail-buttons {
              display: none;
            }
          }
        }
        .product-description {
          text-align: left;
          padding: 10px 15px;
          @include media-breakpoint-down(xs) {/* If media is below 544 */
            text-align: center;
          }
          .cart-btn {
            position: relative;
            bottom: 0;
            opacity: 1;
            -moz-transform: translateY(0);
            -webkit-transform: translateY(0);
            -o-transform: translateY(0);
            -ms-transform: translateY(0);   
          }
          .product-title,.product-price-and-shipping  {
            margin-bottom: 10px;
            opacity: 1;
          }
          .product-desc {
            display: block;
            margin-bottom: 10px;
          }
          .highlighted-informations{
            display: block;
            margin-bottom: 10px;
            position: unset;
            opacity: 1;
            transform: translateX(0);
            -moz-transform: translateX(0);
            -webkit-transform: translateX(0);
            -o-transform: translateX(0);
            -ms-transform: translateX(0);
          }
        }
      }  
    }
  }
}



