#ishispecialproducts {
	#index &{
		@include media-breakpoint-down(md) {/* If media is below 992 */
			margin-bottom:25px;
		}
		margin-bottom: 70px;
	}
	margin-bottom: 10px;
	overflow: hidden;
	display: block;
	.products_block_inner{
		#ishispecialproducts-caraousel{
			.item{
			  &.sold-out{
			    .thumbnail-container{
			      .thumbnail-inner{
			        .product-thumbnail{
			          .outstock-overlay{
			            top: 37%;
			            font-size: 14px;
			          }
			        }
			      }
			    }
			  }
			}
			.product-miniature{
				display: block;
				width: 100%;
				padding: 5px;
				overflow: hidden;
				position: relative;
				.product-container{
					@include media-breakpoint-down(md) {/* If media is below 991 */
						display: block;
					}
					padding: 10px 0;
					overflow: hidden;
					position: relative;
					display: flex;
					align-items: center;
					justify-content: center;
					.thumbnail-container {
						@include media-breakpoint-down(md) {/* If media is below 991 */
							padding: 0 30px;    
						}
						@include media-breakpoint-down(xs) {/* If media is below 544 */
							padding: 0 10px;   
						}
						position: relative;
      					overflow: hidden;
      					display: inline-block;
      					padding: 0 10px;      					
      					.thumbnail-inner{
      					 	.product-thumbnail {
					          display: block;
					          text-align: center;
					          position: relative;
					          img {
					            margin: 0 auto;
					            max-width:100%;
					            border-radius: 50%;
					            -webkit-transition: all 0.7s ease 0s;
					            -moz-transition: all 0.7s ease 0s;
					            -ms-transition: all 0.7s ease 0s;
					            -o-transition: all 0.7s ease 0s;
					            transition: all 0.7s ease 0s; 
					          }
					          .product-img-extra {
					          	&.change{
						            -moz-transform: scale(0,1);
						            -webkit-transform: scale(0,1);
						            -o-transform: scale(0,1);
						            -ms-transform: scale(0,1);
						            transform: scale(0,1);
						        }
					          }
					          img {
					            margin: 0 auto;
					            max-width:100%;
					          }   
        					}
        					.product-flags {
        						display: none;
        					}        					
      					}
					}
					.product-description {
						@include media-breakpoint-down(md) {/* If media is below 991 */
							padding: 0 15px;
							margin-top: 10px;
							text-align: center;
						}
						margin-top: 0; 
						text-align: left;
						padding: 0 5px;
						.product-title {
							@include media-breakpoint-down(md) {/* If media is below 991 */
					    		margin-bottom: 10px;
					    	}
						    display: block;
						    margin-bottom: 7px;
						    a{						    
						    	font-size: 14px;
						    }
					    }
					    .product-price-and-shipping {
					    	@include media-breakpoint-down(md) {/* If media is below 991 */
					    		margin-bottom: 10px;
					    	}
					    	opacity:1;
						    margin-bottom: 5px;
						}
						.product-comments {
							position: relative;
							text-align: left;
							@include media-breakpoint-up(sm){
						        opacity: 1;
						        -webkit-transition: all .4s;
						        -moz-transition: all .4s;
						        -o-transition: all .4s;
						        transition: all .4s;
						     }
						    @include media-breakpoint-down(md) {/* If media is below 991 */
								text-align: center;
							}
							.star_content{
						      .star_on{           
						        &:after{
						          content: "\f005";
						          font-family: "FontAwesome";
						          color: $brand-secondary;
						          -webkit-transition: all 0.7s;
						          -moz-transition: all 0.7s;
						          -ms-transition: all 0.7s;
						          -o-transition: all 0.7s;
						          transition: all 0.7s;
						        }
						        &:before {
						          display: none;
						        }      
						      }    
						    }
						}
						.thumbnail-buttons {
							@media (min-width: 544px) {/* If media is above 543 */
						        bottom: -8px;  
						    }
						    .quickview-btn, .wishlist-btn ,.cart-btn {
						    	@media (min-width: 544px) {/* If media is above 543 */
				                  opacity: 0;
				                  transform: translateY(40px);
				                  -moz-transform: translateY(40px);
				                  -webkit-transform: translateY(40px);
				                  -o-transform: translateY(40px);
				                  -ms-transform: translateY(40px); 
				                }
						    }
						    .quickview-btn{
						    	-webkit-transition: all .3s ease-in-out;
				                -moz-transition: all .3s ease-in-out;
				                -o-transition: all .3s ease-in-out;
				                transition: all .3s ease-in-out;
						    }
						    .wishlist-btn {
						    	-webkit-transition: all .6s ease-in-out;
				                -moz-transition: all .6s ease-in-out;
				                -o-transition: all .6s ease-in-out;
				                transition: all .6s ease-in-out;
						    }
						    .cart-btn {
						    	-webkit-transition: all .9s ease-in-out;
				                -moz-transition: all .9s ease-in-out;
				                -o-transition: all .9s ease-in-out;
				                transition: all .9s ease-in-out;
						    }
						}

					}
		          	&:hover {
		          		.thumbnail-container {
		          			.thumbnail-inner {
		          				.product-thumbnail {
		          					.product-img-extra {
		          						opacity: 0;
		          						&.change{
							                -moz-transform: scale(1,1);
							                -webkit-transform: scale(1,1);
							                -o-transform: scale(1,1);
							                -ms-transform: scale(1,1);
							                transform: scale(1,1);
							                opacity: 1;
							            }
		          					}
		          				}
		          			}
		          		}
		          		.product-description {
		          			.product-comments {
					          @include media-breakpoint-down(xs) {/* If media is below 544 */
					            opacity: 1;
					          }
					          opacity: 0;
					        }
					        .thumbnail-buttons{
					          opacity: 1;
					          .quickview-btn, .wishlist-btn ,.cart-btn  {
					          	opacity: 1;
					            @media (min-width: 544px) {/* If media is above 543 */
				                  opacity: 1;
				                  transform: translateY(0);
				                  -moz-transform: translateY(0);
				                  -webkit-transform: translateY(0);
				                  -o-transform: translateY(0);
				                  -ms-transform: translateY(0);
				                }
					          }
					        }
				      	}
		          	}
				}
			} 
		}
	}	
	.owl-nav {
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			opacity: 1;
		}
		opacity: 0;
	}
	&:hover {
		.owl-nav {
			opacity: 1;
		}
	}
}


