#ishimanufacturerblock {
	margin-bottom:70px;
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom:30px;
	}
	.home-title {
		display: none;
	}
	#manufacturer-carousel {
		.owl-item {
			.item {
				.image-container {
					text-align: center;
					img {
						opacity: 0.8;
						max-width: 130px;
						display: inline-block;
					}
					&:hover {
						img {
							opacity: 1;
						}
					}
				}
			}
		}	
	}
}


