#ishislider {
	overflow:hidden;
	.owl-nav{
		margin-bottom: 0;
		.owl-prev, .owl-next{
			@include media-breakpoint-down(md) {/* If media is below 992 */
				top: 44%;
			}
			@include media-breakpoint-down(sm) {/* If media is below 768 */
				display: none;
			}
			border: 0;
			background-color: $brand-secondary;
			border-radius: 50%;
			position: absolute;
			padding:5px;
	    	top: 46%;
			margin:4px;
			left:70px;
			right:auto;
			width: 40px;
			height: 40px;
			overflow: hidden;
			opacity: 0;
		    -webkit-transition: all 0.3s ease-in-out;
			-moz-transition: all 0.3s ease-in-out;
			-o-transition: all 0.3s ease-in-out;
			transition: all 0.3s ease-in-out;
			&:hover{
				background-color: $brand-primary;				
			}
			i {				
				&:after {
					font-size: 28px;
					line-height: 32px;
					left: 5px;
					right: auto;
					color: $light-color;
					position: absolute;
				}
			}
		}
		.owl-next{
			right: 70px;
			left: auto;
		  	i {			
				&:after {
					right: 5px;
					left: auto;
				}
			}
		}
	}
	.owl-dots {
		position: absolute;
		bottom: 10px;
		right: 0;
		left: 0;
		margin: 0 auto;
		text-align: center;
	    -webkit-transition: all 0.3s;
		-moz-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s; 
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			outline: none;
			span {
				width: 12px;
				height: 12px;
				border: 0;
				background: $brand-primary;
				border-radius: 50%;
				margin:0 3px;
				display: block;
			}
			&.active {
				span {
					background : $brand-secondary;	
				}
			}
		}
	}
	&:hover {
		.owl-nav{
			.owl-prev, .owl-next{
		    	opacity: 1;
			}
			.owl-prev {
				left: 50px;
			}
			.owl-next{
		    	right: 50px;
			}
		}
	}
}


