#ishileftbanners {
	margin-bottom: 30px;
	@include media-breakpoint-down(md) {/* If media is below 992 */
		text-align: center;
		margin-bottom: 15px;
	}
	.image-container {
		position:relative;
		@include media-breakpoint-down(md) {/* If media is below 992 */
			display: inline-block;
		}
		a{
			display: block;
			position: relative;
			overflow: hidden;
			-webkit-transition: all 0.5s;
			-moz-transition: all 0.5s;
			-o-transition: all 0.5s;
			transition: all 0.5s;
			img{
				width: 100%;
				max-width: 100%;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-o-transition: all 0.5s;
				transition: all 0.5s;
			}
		}			
	}
}


