#ishiinstagramblock{
	@include media-breakpoint-down(md){
	    margin-bottom: 30px;
	}
	margin-bottom: 70px;
	position: relative;
	.home-title{
		@include media-breakpoint-down(xs) {/* If media is below 544 */
			position: relative;
			width: 200px;
			background: transparent;
			color: #000;
			font-size: 26px;
  			line-height: 30px;
			padding: 0 0 8px;
			margin-bottom: 25px;
		}
		position: absolute;
		left: 0;
		right: 0;
		margin: 0 auto;
		z-index: 2;
		width: 355px;
		padding: 10px;
		top: 34%;
		font-size: 38px;
		color: $light-color;
		background-color: rgba(226,0,72,.6);
		span {
			@include media-breakpoint-down(xs) {/* If media is below 544 */
				font-size: 20px;
			}
			font-size: 27px;
			line-height: 40px;
			font-weight: 300;
			text-transform: lowercase;
		}
		a{		    
			@include media-breakpoint-down(xs) {/* If media is below 544 */
				color: #000;
			}
			text-decoration: none;
			color: $light-color;
		}
		&:after {
			@include media-breakpoint-down(xs) {/* If media is below 544 */
				display: block;
			}
			display: none;
		}
	}
	#ishiinstagram-carousel{
		position: relative;
		.item{
			.instagram-container{
				a{
					display: block;
					position: relative;
					overflow: hidden;
					-webkit-transition: all 0.5s;
					-moz-transition: all 0.5s;
					-o-transition: all 0.5s;
					transition: all 0.5s;
					img{
						max-width: 100%;
						-webkit-transform-style:unset;
						transform-style:unset;
						-webkit-transition: all 0.9s;
						-moz-transition: all 0.9s;
						-o-transition: all 0.9s;
						transition: all 0.9s;
					}
					.instagram{
						position: absolute;
						top: 50%;
						left: 0;
						right: 0;
						display: none;
						text-align: center;
						margin: 0 auto;
						z-index: 99;
						margin-top: -10px;
						-webkit-transform: scale(0);
						-moz-transform: scale(0);
						-ms-transform: scale(0);
						-o-transform: scale(0);
						transform: scale(0);
						-webkit-transition: all 0.3s;
						-moz-transition: all 0.3s;
						-o-transition: all 0.3s;
						transition: all 0.3s;
						i{
							font-size: 30px;
							color: $light-color;
							display: inline-block;
							vertical-align: middle;							
							border-radius: 10px;
							width: 28px;
							height: 28px;
							line-height: 28px;
							-webkit-transition: all 0.3s;
							-moz-transition: all 0.3s;
							-o-transition: all 0.3s;
							transition: all 0.3s;
							&:hover{								
								background: #d6249f;
								background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
								box-shadow: 0px 3px 10px rgba(0,0,0,.25);
							}
						}
					}
				}
				.text-container{
					position: absolute;
					bottom: 0;
					display: block;
					padding: 10px;
					width: 100%;
					opacity: 0;
					-webkit-transition: all 0.5s;
					-moz-transition: all 0.5s;
					-o-transition: all 0.5s;
					transition: all 0.5s;
					text-transform: capitalize;
					font-size: 14px;
					font-weight: 500;
					font-family: $primary-font;
					color: $light-color;
					.like{
						float: left;
						display: inline-block;
						margin-left: 10px;
						position: relative;			
						margin-bottom: 5px;
						
					}
					.comments{
						float: right;
						display: inline-block;
						margin-right: 10px;
						position: relative;	
						margin-bottom: 5px;	
					}
					i{
						display: block;
						font-size: 16px;
						color: $light-color;
						display: inline-block;
						padding-right: 5px;
						padding-left: 8px; 
					}
				}
			}
			&:hover{
				a{
					img{
						transform: scale(1.5);
						-moz-transform:scale(1.5);
						-webkit-transform:scale(1.5);
						-o-transform:scale(1.5);
						-ms-transform:scale(1.5);
					}
					.instagram{
						-webkit-transform: scale(1);
						-moz-transform: scale(1);
						-ms-transform: scale(1);
						-o-transform: scale(1);
						transform: scale(1);
					}
				}
				.text-container{
					opacity: 1;
				}
			}
		}
	}
}



