.newsletter {
  padding: 0;
  overflow: hidden;
}
.block_newsletter {
  @include search-box;
  font-size: 0.875rem;  
  overflow: hidden;
  display: block;
  background: #f7f7f7;
  padding: 40px 15px;
  border-radius: 4px 0 0 4px;
  p {
    margin:2px 0;
  }
  .conditions {
    display: none;
  }
  .alert-danger , .alert-success{
    padding: 4px; 
  }
  .alert-danger{
    color: #a94442 ! important;
  }
  .alert-success{
    color: #3c763d ! important;
  }  
  .newsletter_text{ 
      @media (max-width: 1390px) {/* If media is below 1300 */
        width: auto;
      }
      @include media-breakpoint-down(md){
        text-align: center;
        padding: 0;
      }         
      overflow: hidden;
      width: 80%;
      margin: 0 auto;
      float: none; 
      .title_block{ 
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          font-size: 18px;
        }
        font-size: 28px;
        font-weight: 500;
        letter-spacing: 0;
        line-height: 47px;
        color: $brand-primary;
        position: relative;
        display: inline-block;
        vertical-align: middle;
        padding-left: 65px;
        text-transform: capitalize;
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 45px;
          height: 45px;
          background: url('../img/email.png') no-repeat center; 
        }
        span {
          color: $brand-secondary;
        }
      }
      p{
        margin: 0;
        font-size: 14px;
        line-height: 24px;
        display:block; 
        color: #666;   
        margin: 22px 0 35px;  
      }
  }  
  .newsletter_form {
    @include media-breakpoint-down(md) {/* If media is below 992 */
      padding: 0;
    }
    text-align: center;
    position: relative;
    padding-bottom: 60px;
    form {
      @media (max-width: 1390px) {/* If media is below 1300 */
        width: auto;
      }
      position: relative;
      width: 80%;
      text-align: center;
      margin: 0 auto;
      height: 100px;
      @include media-breakpoint-down(md) {/* If media is below 992 */
        height: auto;
      }
      .input-wrapper {
        overflow: hidden;
      }
      input[type=submit]{
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          font-size: 15px;
          padding: 4px 30px;
          height: 50px;
        }
        height: 70px;
        background-color: $brand-secondary;
        font-size: 20px;
        font-weight: 400;
        border: none;
        font-family: $secondary-font;
        text-transform: uppercase;
        border-radius: 0 4px 4px 0;
        color: $light-color;
        line-height: 27px;
        padding: 4px 40px;
        right: 15px;
        -webkit-transition: all 0.4s;
        -moz-transition: all 0.4s;
        -o-transition: all 0.4s;
        transition: all 0.4s;
        &:hover {
          background-color: $brand-primary;
        }
      }
        
      input[type=text] {
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          height: 50px;
        }
        min-width: 250px;
        height: 70px;
        border-radius: 4px;
        &:placeholder {
          color: #a4a4a4;
        }
      }
    }
  }
}




