#ishistoreinfo{
	@include media-breakpoint-down(md) {/* If media is below 992 */
		text-align: left;
	}
	text-align: center;
	display: block;
	overflow: hidden;
	.ishistoreinfo-inner{
		.store-logo{
			display:block;
			margin-bottom: 20px;
			text-align: center;
			&:after {
				display: none;
			}
			&:hover {
				padding-left: 0;
			}
		}
		.store-description{
		    margin-bottom: 0;
		    p{
				line-height: 24px;
				margin-bottom:0;
				@include media-breakpoint-up(lg) {/* If media is above 991 */
					width: 85%;
					margin: 0 auto;
				}
		    }
		}
	}
}


