#header {
    color: $brand-primary;
    font-family: $primary-font;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.5px;
    position: relative;    
    #desktop_logo{
      @include media-breakpoint-down(md) {/* If media is below 992 */
        padding: 18px 15px;
      }
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        float: none;
        text-align: center;
        margin: 0 auto;
        padding-bottom: 10px;
      }
      max-width: 240px;
      padding: 20px 15px;
      float: left;
      position: relative;
    }
    .logo {
      max-width: 100%;
    }
    a {
      color: $brand-primary;
      text-decoration: none;
      &:hover {
        color: $brand-secondary;
      }
    }
    .menu-icon {
      float: left;
      cursor: pointer;
    }
    .header-nav {
      @include media-breakpoint-down(sm) {/* If media is below 768 */
        display: none;
      }
      background: #242424;
      #menu-icon {
        vertical-align: middle;
        cursor: pointer;
        margin-left: rem(16px);
        .material-icons {
          line-height: 50px;
        }
      }
      .material-icons {
        line-height: 24px;
      }
    }
    .wrapper-nav {
        @include media-breakpoint-down(lg) {/* If media is below 1200 */
          width: 918px;
          top: 95px;
        }
        @include media-breakpoint-down(md) {/* If media is below 992 */
          width: 693px;
          padding: 0;
          top: 95px;
        }
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          width: 100%;
          position: unset;
        }
        width: 1200px;
        padding-bottom: 15px;
        text-align: center;
        margin: 0 auto;
        max-width: 100%;
        background: #fff;
        box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        z-index: 9;
        top: 70px;
        position: absolute;
        left: 0;
        right: 0;
    }
    .user-info {
      @include media-breakpoint-down(md){ 
        padding: 5px 0;
      }
      cursor: pointer;
      float: right;
      padding: 12px 0;
      margin: 0 15px;
      white-space: nowrap;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      &:hover {        
          .dropdown-menu {
            @include media-breakpoint-down(md){ 
              top: 100%;
            }
            top: 80%;
            visibility: visible;
            opacity: 1;
          }
          .account-logo {
            @include media-breakpoint-up(lg) {/* If media is above 1200 */
              background-position: -67px -68px;
            }
          }
      }
      @include media-breakpoint-down(sm){ 
        padding: 5px;
        margin: 0;
        position: relative;        
      }
      .account-logo {
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          background:url('../img/sprite.png') no-repeat -81px -35px;
        }
        background: url('../img/sprite-header.png') no-repeat -67px -18px;
        width:40px;
        height:40px;
        display: inline-block;
        vertical-align: middle;
      }
      .account-name {
        @include media-breakpoint-down(sm){
          padding: 0;
        }
        display: inline-block;
        vertical-align: middle;
        padding: 5px;
      }
      .dropdown-menu {
        @include media-breakpoint-down(md){ 
          top: 125%;
          min-width: 215px;
        }
        top: 115%;
        visibility: hidden;
        display: block;
        opacity: 0;
        padding: 15px 20px;
        margin: 0;
        right: 0;
        left: auto;
        min-width: 220px;
        border: 1px solid #ebebeb;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
      }     
      .dropdown-menu{
        li{
          a{
            display: block;
            font-size: 12px;
            line-height: 26px;
          }
        }
      }
    }
    .language-selector {
      white-space: normal;
      display: block;
      font-size: 14px;
      border-top: 1px solid #e5e5e5;
      border-bottom: 1px solid #e5e5e5;
      padding: 10px 0;
      margin: 10px 0;
    }
    .currency-selector, .language-selector {
      .language-selector-label, .currency-selector-label {
        font-size: 13px;
        color: #666;
        font-weight: 500;
        text-transform: uppercase;
      }
      li {
        display: inline-block;
        vertical-align: middle;
        width: 31%;
        a{
          padding: 4px 2px;
          margin-right: 5px;
        }
        &.current {
          a{
            color: $brand-secondary;
          }
        }
      }
    }
    .blockcart {
      @include media-breakpoint-down(md){ 
        padding: 5px 0;   
        padding-right: 15px; 
      }
      cursor: pointer;
      float: right;
      padding: 12px 0;
      padding-right: 15px;
      text-align: left;
      position:relative;
      white-space: nowrap;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      @include media-breakpoint-down(sm){ 
        padding: 5px;
        position: relative;        
      }
      &:hover {
        .cart-dropdown {
          @include media-breakpoint-down(md){ 
            top: 100%;
          }
          top: 80%;
          visibility: visible;
          opacity: 1;
        }
        .cart-img {
          @include media-breakpoint-up(lg) {/* If media is above 1200 */
            background-position: -117px -68px;
          }
        }
      }
      .cart-link{
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          background: transparent;
          padding: 0;
        }
        display: block;
        width: 100%;
        overflow: hidden;
        background: #f4f4f4;
        border-radius: 4px;
        padding: 0 14px;
        position: relative;
      }
      .cart-img{
        background: url('../img/sprite-header.png') no-repeat -117px -18px;
        width:40px;
        height:40px;
        display: inline-block;
        vertical-align: middle;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          background:url('../img/sprite.png') no-repeat -40px -34px;
        }
      }
      .cart-content {
        @include media-breakpoint-down(sm){
          display: block;
          padding: 0;
        }
        display: inline-block;
        vertical-align: middle;
        padding: 5px;
        .cart-name{
          display: block;
          text-transform:capitalize;
          position: relative;
          line-height: 21px;
          font-weight: 500;
           @include media-breakpoint-down(md){
            display: none;
           }
        }
      }
      .cart-dropdown{
        @include media-breakpoint-down(md){ 
          top: 120%;
        }
        top: 115%;
        visibility: hidden;
        display: block;
        opacity: 0;
        background: $light-color none repeat scroll 0 0;
        font-family: $primary-font;
        left: auto;      
        position: absolute;
        right: 15px;
        text-align: left;
        width: 350px;
        z-index: 3;
        border: 1px solid #ebebeb;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          width: 300px;  
        }
        &.empty {
          padding:10px;
        }
        /*========================== NO CHANGE ==========================*/
        .product-container{
          max-height: 340px;
          overflow-y: scroll;
          .product{
            border-bottom: 1px solid #ebebeb;
            padding: 5px;
            position: relative;
            overflow:hidden;
            .product-image {
              float: left;
              margin-right:12px; 
              img{
                border: 2px solid #f6f6f6;
                max-width: 90px;
              }
            }
            .product-detail{
              overflow: hidden;
              position: relative;
              font-size: 14px;
              padding-top: 10px;
              font-weight: 300;
              .product-name{
                margin: 0 0 5px 0;
                .cart_block_product_name{
                  white-space: normal;
                    &:hover{
                      color:$brand-secondary;
                    }
                }
              }
              .price{
                   margin: 5px 0;
                   font-size: 16px;
              }
              .label {
                  font-weight: 500;
              }
              .product-atributes {
                li {
                  line-height: 18px;
                }

              }
            }
            .remove-product{
              left: auto;
              position: absolute;
              right: 5px;
              top: 1px;
              .material-icons{
                font-size: 20px;
                &:hover{
                  color:$brand-secondary;
                }
              }
            }
          }
       
        }
        .billing-info{
          padding: 10px;
          .billing{
            margin:5px 0;
            .label{
                float: left;
                font-weight: 500;
            }
            .value{
                float: right;
                font-weight: 400;
            }
          }
        }
        .cart-btn{
          padding: 10px 15px;
          text-align: center;
          a {
            color:$light-color;
            text-transform: uppercase;
          }
        }
      }
      .cart-products-count {
        line-height: 14px;
        display: block;
        width: auto;
        letter-spacing: 1px;
        text-transform: capitalize; 
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          border-radius: 50%;
          height: 15px;
          width: 15px;
          background: $brand-primary;
          color: $light-color;
          font-size: 11px;
          right: 6px;
          top: 4px;
          position: absolute;
          left: auto;
          bottom: auto;
          text-align: center;
          margin:0;
        }       
      }
      a {
        color: $brand-primary;
        font-weight: 500;
      }
      .header {
        padding-right: 0;
        margin-top: 0;
      }
    }
    .nav-full-width {
      > .container {
        position: relative;
      }
      background-color: transparent;     
      position: relative; 
      @include media-breakpoint-down(sm) {/* If media is below 768 */
        background-color: $brand-secondary;
      }
      &.fixed-header {
        @include media-breakpoint-down(sm) {/* If media is below 768 */
          background-color: $brand-secondary;
        }
        background-color: #f5f5f5;
        position: fixed;
        top:0; 
        left:0;
        right: 0;
        text-align: center;
        margin: 0 auto;
        z-index:99;
        box-shadow: 0px 0px 4px rgba(241, 241, 241, 0.3);
        animation: slide-down 0.7s;
        -moz-animation: slide-down 0.7s;
        -webkit-animation: slide-down 0.7s;
        -o-animation: slide-down 0.7s;
        overflow: visible;
        #_desktop_top_menu {
          float: left;
        }
        #ishiheaderlinks_block {
          @include media-breakpoint-down(md){
            float: right;
          }
          float: left;
        }
        #_desktop_seach_widget {          
          position: unset;
        }
      }
    }
    @keyframes slide-down {
      0% {
        transform: translateY(-100%);
        -webkit-transform: translateY(-100%);
        -ms-transform:translateY(-100%);
        -o-transform:translateY(-100%);
        -moz-transform:translateY(-100%);
      } 
      100% {
        transform: translateY(0);
        -webkit-transform: translateY(0);
        -ms-transform:translateY(0);
        -o-transform:translateY(0);
        -moz-transform:translateY(0);
      } 
    }
}

#_desktop_seach_widget {
  @include media-breakpoint-up(lg) {/* If media is above 991 */
    display: inline-block;
    vertical-align: middle;
    padding: 0 15px;
    float: right;
    position: absolute;
    right: 0;
    top: 0;
  }
}
.search-widget { 
  @include search-box;
  cursor: pointer;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  @include media-breakpoint-down(md) {
    padding: 5px;
    float: right;
    position: relative;
  }
  .search-logo{
    @include media-breakpoint-down(md){
      background:url('../img/sprite.png') no-repeat 0 1px;
      width:40px;
      height:40px;
      display:block;
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
      background-position: 0 -35px;
    }
  }
  form {
    @include media-breakpoint-down(md) {/* If media is below 992 */
      top: 125%;
      visibility: hidden;
      opacity: 0;
      z-index: 2;
      position: absolute;
      right: 0;
    }
    display: block;
    z-index: 2;
    position: relative;
    top:5px;
    right: 0;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    input[type="text"] {      
      min-width: 300px;
      width: 100%;
      border: 1px solid #eee;
      border-radius: 4px;
      height: 35px;
      padding: 5px 10px;
      background:#f5f5f5;
      text-transform: capitalize;
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 0.8px;
      color: #a4a4a4;
      &:focus{
        @include media-breakpoint-down(md) {
          background: #f5f5f5;
        }
        background: transparent;
      }
      &::placeholder{
        color: #a4a4a4;
      }
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        min-width: 214px;
      }
    }
    button[type="submit"] {
      position: absolute;
      background: none;
      border: none;
      right: 0;
      top: 0;
      color: $light-color;
      padding: 0;
      height: 35px;
      width: 40px;
      border-radius: 0px 4px 4px 0;
      background: $brand-secondary;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      &:hover {
        background: $brand-primary;
        color: $light-color;
        @include media-breakpoint-down(md) {
          background: transparent;
          i {
            color: $brand-primary;
          }
       }
      }

      @include media-breakpoint-down(md) {
        background: transparent;
        i {
          color: $brand-secondary;
        }
      }
    }
  }
  &:hover {
    form {
      @include media-breakpoint-down(md) {/* If media is below 992 */
        top: 100%;
        visibility: visible;
        opacity: 1;
      }
    }
  }
}

.home-title {
    @include media-breakpoint-down(lg) {/* If media is below 1200 */
      font-size: 34px;
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      font-size: 26px;
      line-height: 30px;
    }
    position: relative;
    text-align: center;
    text-transform: uppercase;
    font-size: 40px;
    line-height: 40px;
    padding: 0 0 8px;
    margin-bottom: 25px;
    font-weight: 500;
    color: $brand-primary;
    font-family: $primary-font;
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0; 
      right: 0;
      width: 70px;
      height: 1px;
      text-align: center;
      margin: 0 auto;
      background: #e9e9e9;
    }    
  }


#footer {
  #index &{
    background: $light-color;
    margin-top: 0;
  }
  margin-top: 15px;
  clear:both;
  background: #f7f7f7;
  color:#000;
  a, p{
    font-family: $primary-font;
    font-weight: 400;
    letter-spacing: 0.5;
    font-size: 14px;
    color:#666;
    text-decoration: none;
    position: relative;
    overflow: hidden;
  }
  a{
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 40%;
      width: 5px;
      background: $brand-secondary;
      height: 5px;
      opacity: 0;
      border-radius: 50%;
      transition: all .4s ease 0s;
      -webkit-transition: all .4s ease 0s;
      -moz-transition: all .4s ease 0s;
      -o-transition: all .4s ease 0s;
    }
  }
  h3,.h3{
    margin-bottom: 20px;
    color: #000;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
  }
  .footer-container{
    padding: 30px 0 30px 0;
    .block-heading {
      color : #000;
    }
    a{
      -webkit-transition: all 0.2s;
      -moz-transition: all 0.2s;
      -o-transition: all 0.2s;
      transition: all 0.2s;
      &:hover{
        color:$brand-secondary;
        padding-left: 20px;
        &:after {
          opacity: 1;
        }
      }
    }
  }
  .block-contact {
    .block {
      .content {
        a {
          &:hover {
            padding-left: 0;
          }
        }
      }
    }
  }
  #block_myaccount_infos{
    display: block;
    overflow: hidden;  
  }
  .footer-after {
    background: #333;
    color: $light-color;
    .footer-aftertext {
      text-align: left;
      @include media-breakpoint-down(md) {/* If media is below 992 */
        text-align: center;   
      }
      margin-bottom:0;
      padding:10px 0;
      a{
        color: $light-color;
      }
    }
    >.container {
      @include media-breakpoint-down(sm) {/* If media is below 544 */
        padding: 15px 15px 60px;
      }
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }
}

.block-contact {
  text-align: left;
  display: block;
  overflow: hidden;
  .block{
    margin-bottom: 15px;
    letter-spacing: 0.5px;
    font-size: 14px;
    font-weight: 400;
    
    &:last-child{
      @include media-breakpoint-down(md) {/* If media is below 992 */
        margin-bottom: 0;
      }
    }
    &.address{
      .icon{
        &:before{
          content: "\f041"; 
        }
      }
    }
    &.phone{
      .icon{
        &:before{
          content: "\f095";
        }
      }
    }
    &.email{
      .icon{
        &:before{
          content: "\f0e0";
        }
      }
    }
    .icon{
      content: "";
      color: #666;
      font-family: FontAwesome;
      font-size: 18px;
      height: 30px;
      width: 30px;      
      line-height: 27px;
      vertical-align: middle;
      display: inline-block;
      text-align: center;
      i{
        display: none;
      }
    } 
    .content{
      line-height: 25px;
      overflow: hidden; 
      display: inline-block;
      vertical-align: middle;
      padding-left: 5px;   
      color: #666;  
      a{
        &:after {
          display: none;
        }
      }
    }
  }
}  
.block-social { 
  text-align: center;
  display: block;
  overflow: hidden;
  margin: 30px 0 40px;
  @include media-breakpoint-down(md){
   text-align: left;
   margin: 0;
  }
  .social_text {
    display: none;
  }
  p{
    display: none;
    margin-top: 0;
    margin-bottom: 15px;
  }
  ul{
    margin-top: 0;
    margin-bottom:0;
    li{
      cursor: pointer;
      display: inline-block;
      vertical-align: middle;
      height: 50px;
      width: 50px;
      padding: 9px;
      margin: 3px;
      border-radius: 50%;
      text-align: center;
      position: relative;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
      a {
        width: 50px;
        height: 50px;
        text-align: center;
        margin: 0 auto;
        text-decoration: none;
        &:hover{
          padding:0 ! important;
          color: transparent;
        }
        &:after {
          display: none;
        }
        &:before {
          display: inline-block;
          vertical-align: middle;
          font-family: FontAwesome;
          font-weight: 400;
          position: relative;            
          font-size: 18px;
          line-height: 33px;
          color: $light-color;
        }
        .socialicon-label {
          display: none;
        }
      }    
      &.facebook{
        background-color:#3b5999;
        a{          
          &:before{
            content: "";
          } 
        } 
      }
      &.twitter{
        background-color: #00aced;
        a{
          &:before{
              content: "\f099";
          }
        }
      }
      &.youtube{
        background-color: #d12121;
        a{
           &:before{
            content:"\f16a";
          }
        }
      } 
      &.googleplus{
        background-color: #dc473b;
        a{
           &:before{
            content:"\f0d5";
          }
        } 
      } 
      &.rss{
        background-color: #f26522;
        a{
          &:before{
              content:"\f09e";
          } 
        }
      }
      &.pinterest{
        background-color: #cb2027;
        a{
          &:before{
            content:"\f231";
          }
        }
      }
      &.vimeo{
        background-color: #00aced;
        a{
          &:before{
            content:"\f27d";
          }
        }
      }
      &.instagram{
        background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); 
        background: -webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
        background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%);
        a{ 
          &:before{
            content:"\f16d";
          }
        }  
      }
      span{
        display: none;
      }
      &:hover{
        background: $brand-secondary;
      }
    }
  }
}
.linkedlist {
  display: block;
  overflow: hidden;
  .linkblock {
    @include media-breakpoint-down(md) {/* If media is below 992 */
      margin-bottom: 15px;
    }
  }
}

/*---------------responsive-----------*/
.footer-container ,.footer-before{
  overflow: hidden;
  li {
    margin-bottom: 15px;
    &:last-child{
      @include media-breakpoint-down(md) {/* If media is below 992 */
        margin-bottom: 0;
      }
    }
  }
  .footer-dropdown {
    margin-top:10px;
  }
  @include media-breakpoint-up(lg) {/* If media is above 992 */
    .collapse {
        display: inherit;
    }
    .footer-dropdown {
      margin-top:0;
    }
  }
  @include media-breakpoint-down(md) {/* If media is below 992 */
    .footer-block {
      margin-bottom:15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .footer-dropdown {
      margin-bottom:0px;
    }
  }
}

.footer-title{
    position: relative;
    cursor:pointer;
    .navbar-toggler {
        position: absolute;
        top:12px;
        right: 15px;
        padding:0;
    }
}
.footer-title {
    .navbar-toggler {
        i {
          &.add {
            display: none;
          }
          &.remove {
            display: block;
          }
        }
    }
    &.collapsed{
      .navbar-toggler {
        i {
          &.add {
            display: block;
          }
          &.remove {
            display: none;
          }
        }
      }
    }
}

.footer-title {
  .navbar-toggler {
        top:-2px;
        right: 0;
        color: $brand-primary;
  }
}

#slidetop{
  background: $brand-secondary none repeat scroll 0 0;
  box-shadow: 0 0 10px 4px rgba(255, 255, 255, 0.1);
  color: transparent;
  border-radius: 50%;
  bottom: 20px;
  cursor: pointer;
  height: 40px;
  overflow: hidden;
  padding: 6px;
  position: fixed;
  right: 20px;
  text-align: center;
  width: 40px;
  z-index: 99;
  display: none;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  &::after{
    content: '\E5CE';
    display: inline-block;
    color:$light-color;
    font-family: material icons;
    font-size: 26px;
    left: 0;
    position: absolute;
    right: 0;
    top: 10px;
  }
  &:hover{
    background-color: $brand-primary;
  }
}

#siteloader {
  background: #fff;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease;
  .loader{
    display: block;
    position: relative;
    top: 40%;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    text-align: center;
    margin: 0 auto;
    border: 3px solid transparent;
    border-top-color: transparent;
    border-top-color: $brand-secondary;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
    &:before{
      content: "";
      position: absolute;
      top: 5px;
      left: 5px;
      right: 5px;
      bottom: 5px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: $brand-secondary;
      -webkit-animation: spin 3s linear infinite;
      animation: spin 3s linear infinite;
    }
    &:after{
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: transparent;
    border-top-color: $brand-secondary;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
    }
  }
}
@keyframes spin{
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}  


