#ishiservices{
	text-align: center;
	padding: 55px 0;	
	background: #242424;
	@include media-breakpoint-down(lg){
		padding: 47px 0;
	}
	@include media-breakpoint-down(md) {/* If media is below 992 */
		padding: 45px 0;
	}
	@include media-breakpoint-down(xs) {/* If media is below 544 */
		padding: 20px 0;
	}
	#ishiservices-content{
		@include media-breakpoint-down(lg){
			top: -30px;
		}
		@include media-breakpoint-down(md) {/* If media is below 992 */
			top: -32px;
		}
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			top: -29px;
		}
		@include media-breakpoint-down(xs) {/* If media is below 544 */
			top: 0;
		}
		display: block;
		overflow: hidden;
		position: relative;
		top: -36px;
		text-align: center;
		margin: 0 auto;
		.services{
			text-align: center;
			@include media-breakpoint-down(sm) {/* If media is below 768 */
				margin-bottom: 3px;
				border-right: 0;
			}
			cursor: pointer;	
			border-right: 1px solid #454545;
			&:last-child {
				border-right: 0;
				margin-bottom: 0;
			}
			&:nth-child(2n+2) {
				@include media-breakpoint-down(md) {/* If media is below 992 */
					border-right: 0;
				}
			}
			.service-img{
				background: url(../img/cms-service.png) no-repeat;
				width: 25px;
				height: 25px;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				position: relative;
				transition: all 400ms;
				-webkit-transition: all 400ms;
				-moz-transition: all 400ms;
				-ms-transition: all 400ms;
				-o-transition: all 400ms;
			}	
			&.delivery {
				.service-img{
					background-position: -22px -22px;
				}
			}
			&.gift {
				.service-img{
					background-position: -23px -65px;
				}
			}
			&.money {
				.service-img{
					background-position: -21px -108px;
				}
			}
			&.support {
				.service-img{
					background-position: -22px -151px;
				}
			}			
			.service-block{
				@include media-breakpoint-down(lg) {/* If media is below 1200 */	
					text-align: center;
					padding-left: 0;
				}
				@include media-breakpoint-down(md) {/* If media is below 992 */
					text-align: left;
					padding-left: 10px;
				}
				display: inline-block;
				vertical-align: middle;
				text-align: left;
				padding-left: 10px;
				.service-title{	
					font-size: 14px;
				    font-weight: 400;
				    text-transform: capitalize;
				    color: $light-color;
				    opacity: 1;
				    font-family: $primary-font;
				    transform: translateY(9px);
				    -moz-transform:translateY(9px);  
					-webkit-transform:translateY(9px);  
					-o-transform:translateY(9px);  
					-ms-transform:translateY(9px); 
				    transition: all 400ms ease;
					-webkit-transition: all 400ms ease;
					-moz-transition: all 400ms ease;
					-ms-transition: all 400ms ease;
					-o-transition: all 400ms ease;
				}
				.service-desc{
					display: block;
					color: #fff;
					opacity: 0;
					font-weight: 400;
					font-size: 12px;
					transform: translateY(0);
					-moz-transform:translateY(0);  
					-webkit-transform:translateY(0);  
					-o-transform:translateY(0);  
					-ms-transform:translateY(0); 
					-webkit-transition: all 400ms ease;
					-moz-transition: all 400ms ease;
					-ms-transition: all 400ms ease;
					-o-transition: all 400ms ease;
				}
			}
			&:hover{
				.service-img{
					transform: rotateY(360deg);	
					-moz-transform: rotate(360deg);
					-webkit-transform: rotate(360deg);
					-o-transform: rotate(360deg);
					-ms-transform: rotate(360deg);			
				}
				&.delivery {
					.service-img{
						background-position: -67px -22px;
					}
				}
				&.gift {
					.service-img{
						background-position: -67px -65px;
					}
				}
				&.money {
					.service-img{
						background-position: -67px -108px;
					}
				}
				&.support {
					.service-img{
						background-position: -67px -151px;
					}
				}
				.service-block{
					.service-title{	
						opacity: 0;					
				    	transform: translateY(-20px);
				    	-moz-transform:translateY(-20px);  
						-webkit-transform:translateY(-20px);  
						-o-transform:translateY(-20px);  
						-ms-transform:translateY(-20px); 
					}
					.service-desc {
						opacity: 1;
						transform: translateY(-9px);
						-moz-transform:translateY(-9px);  
						-webkit-transform:translateY(-9px);  
						-o-transform:translateY(-9px);  
						-ms-transform:translateY(-9px); 
					}
				}
			}
		}
	}    
}


