#ishiproductsblock{
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom:20px;
	}
	margin-bottom:35px;
	.ishiproductsblock-container{
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			text-align: center;
		}
		#ishiproductstab{
			@include media-breakpoint-down(sm) {/* If media is below 768 */
				display: inline-block;
				vertical-align: middle;
				float: none;
				margin-bottom: 10px;
			}
    		position: relative;
			border: 0;
			text-align: center;
			margin: 0 auto;
			margin-bottom: 20px;
			.nav-item {	
				@include media-breakpoint-down(xs){/* If media is below 544 */
					display: block;
					margin-bottom: 8px;
					text-align: center;
				}
				&:last-child{
					@include media-breakpoint-down(xs){/* If media is below 544 */
						margin-bottom:0px;
					}
				}
    			display: inline-block;
    			vertical-align: middle;
    			margin-right: 5px;
    			float: none;
    			&.active, &:hover{
    				.nav-link{
						background: $brand-secondary;
						color: $light-color;
					}
			    }
    			.nav-link{
    				@include media-breakpoint-down(xs){
			    		padding: 7px 20px;
			    		display: inline-block;
			    	}
    				font-size: 16px;
    				font-weight: 400;
				    background: transparent none repeat scroll 0 0;
				    border: medium none;
				    color: $brand-primary;
				    margin: 0;
				    outline: medium none;
				    padding: 10px 20px;
				    border-radius: 0px;
				    position: relative;
				    text-transform: capitalize;
				    transition: all 400ms ease-in-out 0s;
				    -webkit-transition: all 400ms ease-in-out 0s;
					-moz-transition: all 400ms ease-in-out 0s;
					-o-transition: all 400ms ease-in-out 0s;
				    
    			}
    		}	
		}		
	}
}


