#ishitopbanners{
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom: 30px;
	}
	color: $light-color;
	margin-bottom:70px;
	position: relative;
	.ishitopbanners-container{	
		overflow: hidden;
		position: relative;
		padding: 0;
		&:nth-child(2n+1) {
			.image-container{
				.banner-data {
					.banner-title{
						color: #1b3173;
					}
				}
			}
		}
		&:nth-child(2n+2) {
			.image-container{
				.banner-data {
					.banner-title{
						color: #e65e5e;
					}
				}
			}
		}
		&:nth-child(2n+3) {
			.image-container{
				.banner-data {
					.banner-title{
						color: #b32247;
					}
				}
			}
		}
		.image-container{
			overflow: hidden;
			display: block;
			position: relative;	
			img{
				max-width: 100%;
				width: 100%;
				-webkit-transform-style: unset;
				transform-style: unset;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-ms-transition: all 0.5s;
				-o-transition: all 0.5s;
				transition: all 0.5s;
			}	
			.banner-data{	
				@include media-breakpoint-down(lg) {/* If media is below 1200 */
					top: 24%;
				}
				@include media-breakpoint-down(md) {/* If media is below 992 */
					top: 21%;
				}
				@include media-breakpoint-down(sm){
					top: 24%;
				}
				@media screen and (min-width: 768px) and (max-width: 1090px) {/* If media is between 768 and 1090 */
					top: auto;
					bottom: 10px;
					right: 10px;
				}
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					top: auto;
					bottom: 10px;
					right: 10px;
				}
				top: 35%;
				position: absolute;
				right: 15px;	
				padding: 5px;
				text-align: left;
				display: inline-block;
				vertical-align: middle;
				.banner-title{
					@media screen and (min-width: 768px) and (max-width: 1090px) {/* If media is between 768 and 1090 */
						font-size: 12px;
					}
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						font-size: 12px;
					}
					text-transform: uppercase;
					font-weight: 500;
					font-size: 15px;
					line-height: 16px;
					color: $brand-secondary;
					-webkit-transition: all 0.7s;
					-moz-transition: all 0.7s;
					-ms-transition: all 0.7s;
					-o-transition: all 0.7s;
					transition: all 0.7s;						
				}				
				.banner-subtitle{
					text-transform: capitalize;
					font-weight: 400;
					font-size:38px;
					margin: 25px 0 30px;
					line-height: 40px;
					color: $brand-primary;
					span {
						color: #212121;
						font-weight: 600;
					}
					@media (max-width: 1400px){
						font-size: 27px;
						line-height: 25px;
					}
					@media screen and (min-width: 768px) and (max-width: 1090px) {/* If media is between 768 and 1090 */
						margin: 15px 0 20px;
						font-size: 20px;
						line-height: 25px;
					}
					@include media-breakpoint-down(md) {/* If media is below 992 */
						font-size: 18px;
						line-height: 20px;
					}
					@include media-breakpoint-down(sm) {/* If media is below 768 */
						font-size:38px;
						line-height: 40px;
					}
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						margin: 15px 0 20px;
						font-size: 20px;
						line-height: 25px;
					}
				}	
				.banner-btn {
					@include media-breakpoint-down(lg) {/* If media is below 1200 */
						font-size: 12px;
					}
					background-color: $brand-primary;
					color: $light-color;
					font-size: 15px;
					font-weight: 400; 
					padding: 10px 15px;
					line-height: 1;
					border-radius: 4px;
					text-transform: uppercase;
					display: inline-block;
					vertical-align: middle;
					text-align: center;
					-webkit-transition: all 0.5s;
					-moz-transition: all 0.5s;
					-o-transition: all 0.5s;
					transition: all 0.5s;
					a{
						color: $light-color;
					}
					&:hover {
						background-color: $brand-secondary;
					}
				}	
			}			
		}
		&:hover{
			@include media-breakpoint-down(xs){
				opacity: 1;
			}
			.image-container{
				img {
					-moz-transform: scale(1.09);
					-webkit-transform: scale(1.09);
					-o-transform:scale(1.09);
					-ms-transform: scale(1.09);
					transform: scale(1.09);
				}
			}
		}			
	}	
}



