#smartblog_block{
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom:30px;
		padding: 0 15px;
	}
	margin-bottom:70px;
	position: relative;	
	padding: 40px 15px;
	.products_block_inner{
		position: relative;
		text-align: center;
		.home-title{		
			a{
				color: $brand-primary;
			}
		}
		#smartblog-carousel{
			.sds_blog_post{	
				&:hover{
					.blog_post{
						.news_module_image_holder{
							a{
								img{
									-webkit-transform-style:unset;
	    							transform-style:unset;
								}
								.blog-hover {
									opacity: 1;
								}
							}
						}							
					}			
				}		
				&:nth-child(2n) {
					.blog_post {
						.news_module_image_holder {
							@include media-breakpoint-down(xs) {/* If media is below 544 */
								text-align: center;
							}
							float: right;
						}
						.blog_content {
							@include media-breakpoint-down(xs) {/* If media is below 544 */
								text-align: center;
							}
							float: left;
							text-align: right;
						}
					}
				}
				&:nth-child(2n+1) {
					.blog_post {
						@media (max-width: 1390px) {/* If media is below 1300 */
							margin-bottom: 10px;
						}
						.news_module_image_holder {
							@include media-breakpoint-down(xs) {/* If media is below 544 */
								text-align: center;
							}
							float: left;
						}
						.blog_content {							
							@include media-breakpoint-down(xs) {/* If media is below 544 */
								text-align: center;								
							}
							float: right;
							text-align: left;
						}
					}
				}
				.blog_post{
					@include media-breakpoint-down(lg) {/* If media is below 1200 */
						padding: 5px 15px;
					}
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						padding: 5px 0;
					}
					position: relative;
					display: inline-block;
					vertical-align: middle;
					padding: 5px 25px;
					.news_module_image_holder{
						display: inline-block;
						vertical-align: middle;
						position: relative;
						border-radius: 50%;
						a {
							position: relative;
							display: inline-block;
							vertical-align: middle;
							img{
								width: 100%;
								max-width: 150px;
								border-radius: 50%;
								transition: all 0.5s;
								-webkit-transition: all 0.5s;
								-moz-transition: all 0.5s;
								-ms-transition: all 0.5s;
								-o-transition: all 0.5s;
							}
							.blog-hover {
							    background: rgba(0,0,0,.5);
							    position: absolute;
							    left: 0;
							    right: 0;
							    bottom: 0;
							    top: 0;
							    border-radius: 50%;
							    opacity: 0;
							    -webkit-transition: all 0.5s;
								-moz-transition: all 0.5s;
								-ms-transition: all 0.5s;
								-o-transition: all 0.5s;
							}
						}						
					}
					.blog_content{
						@media (max-width: 1390px) {/* If media is below 1300 */
							padding: 10px 0;
						}
						@include media-breakpoint-down(xs) {/* If media is below 544 */
							padding: 5px 15px;
						}
						padding: 20px 0;
						display: inline-block;
						vertical-align: middle;
						font-family: $secondary-font;
						.sds_post_title{
							margin-bottom: 15px;
							a{
								font-size: 18px;
								font-family: $secondary-font;
								text-transform: capitalize;
								font-weight: 500;
								color: #010101
							}
						}
						p{
							margin-bottom: 0;
							font-size: 14px;
							color: #666;
							font-family: $primary-font;
						}
						.r_more{
							display: none;
						}
						.smartbloginfo{
							font-family: $secondary-font;
							color: $brand-primary;
							font-size: 14px;
							font-weight: 400;
							margin-bottom: 15px;
							-webkit-transition: all 0.5s;
							-moz-transition: all 0.5s;
							-o-transition: all 0.5s;
							transition: all 0.5s;
							.blog_date{
								i{
									display: inline-block;
									font-size: 16px;
									color: $brand-secondary;
									padding-right: 5px;
								}
								.day_date, .day_year {
									display: none;
								}
							}
							.author{
								display: none;
							}
							.comment {
								display: none;
							}
						}												
					}
				}		
			}
		}
	}
	.bx-wrapper {
		margin: 0;
		.bx-controls.bx-has-pager {
			@media (max-width: 1390px) {/* If media is below 1300 */
				position: unset;
				display: inline-block;
				vertical-align: middle;
				padding: 0;
				margin-top: 20px;
				box-shadow: none;
				background: transparent;
			border-radius:0;
			}
		    position: absolute;
		    top: 0;
		    right: -22px;
		    background: #fff;
		    padding: 25px 20px;
		    box-shadow: 0 0px 6px 1px rgba(0, 0, 0, 0.1);
		    z-index: 1;
			overflow: hidden;
			border-radius:4px;
			.bx-pager {
				@media (max-width: 1390px) {/* If media is below 1300 */
					width: auto;
				}
				position: relative;
				bottom: 0;
				width: 5px;
				padding: 0;
				.bx-pager-item {
					@media (max-width: 1390px) {/* If media is below 1300 */
						display: inline-block;
						vertical-align: middle;
					}
					.bx-pager-link {
						@media (max-width: 1390px) {/* If media is below 1300 */
							width: 30px;
							height: 5px;
							margin: 0 7px 0 0;
						}
						width: 5px;
						height: 30px;
						margin: 0 0 26px;
						background: $brand-secondary;
						display: inline-block;
						vertical-align: middle;
						border-radius: 0;
						&.active {
							background: $brand-primary;
						}
					}
					&:last-child {
						.bx-pager-link {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}




